import {Action, lazyGet, lazyInject} from "@dp-instant/core";
import {inject, injectable} from "inversify";
import {Howl} from "howler";
import {TypesAudio} from "../TypesAudio";
import {AudioModel} from "../model/AudioModel";

@injectable()
export class PlaySoundAction<T = string> extends Action<T> {
    @inject(TypesAudio.model)
    protected model: AudioModel<any>;

    protected get howl(): Howl | undefined {
        return lazyGet(TypesAudio.soundsHowl);
    }

    async execute(id: T): Promise<void> {
        if (this.model.soundsMuted()) {
            return;
        }
        const soundName = this.transformSound(id);
        if (soundName) {
            this.howl?.play(soundName);
        }
    }
    protected transformSound(type: T): string | undefined {
        return (type as unknown) as string;
    }
}
