import LeaderboardEntry = FBInstant.LeaderboardEntry;
import {IEventEmitter} from "@dp-instant/core";

export enum ScoreType {
    SESSION = 1 << 0,
    CONTEXT = 1 << 1,
    GLOBAL = 1 << 2
}
export enum ScoreEvent {
    UPDATED = "UserScore.UPDATED",
    INCREASED = "UserScore.INCREASED"
}

export interface IScore extends IEventEmitter {
    setScoreSession(value: number): void;

    update(context?: LeaderboardEntry, global?: LeaderboardEntry): void;

    getScore(mask?: ScoreType | number): number | undefined;

    getEntry(type: ScoreType): LeaderboardEntry | undefined;

    flush(): void;
}
