import {inject, injectable} from "inversify";
import {
    Action,
    CommonEvents,
    di,
    GAME_SCRIPT_ORIGIN,
    GlobalEventProvider,
    lazyGet,
    log
} from "@dp-instant/core";
import {AdEvents, ISocial, TypesSocial} from "@dp-instant/social";
import {TypesAudio} from "../TypesAudio";
import {Howl, HowlOptions} from "howler";
import {PlayMusicActionPayload} from "../actions/PlayMusicAction";

export interface AudioModelConfig {
    sounds?: HowlOptions;
    music?: HowlOptions;
}
@injectable()
export class AudioModel<T extends AudioModelConfig = any> extends GlobalEventProvider {
    static readonly COOKIE_KEY = "audio";
    static readonly MUTE_MUSIC = "AudioModel.MUTE_MUSIC";
    static readonly MUTE_SOUNDS = "AudioModel.MUTE_SOUNDS";
    private _sounds: boolean;
    private _music: boolean;
    private _paused = false;
    private _started = false;
    private _ready = false;
    protected _musicHowl?: Howl;
    protected _soundsHowl?: Howl;
    @inject(TypesSocial.model)
    protected social: ISocial;
    private _windowFocusHandler = (e: Event) => this.windowFocusHandler(e);

    constructor() {
        super();
        if (process.env.NODE_ENV === "development") {
            (window as any).v_audio = this;
        }
    }

    public async init(params?: T): Promise<this> {
        ["blur", "focus"].forEach(e =>
            window.addEventListener(<keyof WindowEventMap>e, this._windowFocusHandler)
        );

        this.addListener(AdEvents.STARTED, this.onAdStarted);
        this.addListener(AdEvents.ENDED, this.onAdEnded);
        this.addListener(CommonEvents.PAUSE, this.onPause);

        type AudioConfig = {music: boolean; sounds: boolean};
        const {audio: audioConfig} = await this.social.cookie.get(AudioModel.COOKIE_KEY);
        const fixPaths = (obj: {src?: string | string[]}) => {
            if (Array.isArray(obj.src)) {
                obj.src = obj.src.map(src => `${GAME_SCRIPT_ORIGIN}${src}`);
            } else if (typeof obj.src === "string") {
                obj.src = `${GAME_SCRIPT_ORIGIN}${obj.src}`;
            }
            return obj;
        };
        if (params) {
            if (params.sounds) {
                this._soundsHowl = new Howl(fixPaths(params.sounds));
                di.bind(TypesAudio.soundsHowl).toConstantValue(this._soundsHowl);
            }
            if (params.music) {
                this._musicHowl = new Howl(params.music);
                di.bind(TypesAudio.musicHowl).toConstantValue(this._musicHowl);
            }
        }

        this.muteMusic((audioConfig as AudioConfig)?.music || false);
        this.muteSounds((audioConfig as AudioConfig)?.sounds || false);
        this._ready = true;
        return this;
    }

    public soundsMuted(): boolean {
        return this._sounds;
    }

    public musicMuted(): boolean {
        return this._music;
    }

    public muteSounds(value: boolean) {
        if (this._sounds !== value) {
            this._sounds = value;
            this.dispatch(AudioModel.MUTE_SOUNDS, value);
            this.postData();
        }
        return this._sounds;
    }

    public muteMusic(value: boolean) {
        if (this._music !== value) {
            this._music = value;
            this.tapMusic();
            this.dispatch(AudioModel.MUTE_MUSIC, value);
            this.postData();
        }
        return this._music;
    }

    /**
     * Called once the level is ready
     */
    public activateBackgroundMusic() {
        this._started = true;
        this.tapMusic();
    }

    get ready(): boolean {
        return this._ready;
    }
    get paused(): boolean {
        return this._paused;
    }
    set paused(value: boolean) {
        this._paused = value;
        this.tapMusic();
    }

    protected onAdStarted() {
        this.paused = true;
    }
    protected onAdEnded() {
        this.paused = false;
    }
    protected onPause(doPause: boolean) {
        this.paused = doPause;
    }
    private postData() {
        const {_sounds: sounds, _music: music} = this;
        this.social.cookie.save(AudioModel.COOKIE_KEY, {sounds, music});
    }

    protected tapMusic() {
        if (this._started) {
            this.runMusicAction(!this._music && !this._paused);
        }
    }
    protected runMusicAction(active: boolean) {
        return lazyGet<Action<PlayMusicActionPayload>>(TypesAudio.musicAction)?.run({active});
    }
    public windowFocusHandler(e: Event) {
        switch (e.type) {
            case "focus":
                this.paused = false;
                break;
            case "blur":
                this.paused = true;
                break;
        }
    }
    public destroy() {
        ["blur", "focus"].forEach(e =>
            window.removeEventListener(<keyof WindowEventMap>e, this._windowFocusHandler)
        );
        super.destroy();
    }
}
