export type Locale =
    | "en_US"
    | "ru_RU"
    | "es_ES"
    | "es_LA"
    | "pt_PT"
    | "pt_BR"
    | "fr_FR"
    | "de_DE"
    | "zh_CN"
    | "zh_TW"
    | "ja_JP"
    | "ko_KR"
    | "it_IT"
    | "pl_PL"
    | "tr_TR"
    | "th_TH"
    | "hi_IN"
    | "vi_VN"
    | "id_ID"
    | "ms_MY";

type LocalizableString = {
    default: string;
    localizations?: {
        [key in Locale]?: string;
    };
};
const determineAvailableLocaleCode = (
    locale: Locale,
    localizations?: {[key in Locale]?: string}
) => {
    const [lang, locality] = locale.split("_");
    if (!localizations || lang === "en") {
        return; // undefined means default
    }
    const stringLocalizationKeys = Object.keys(localizations);
    const variants: Locale[] = stringLocalizationKeys.filter(
        key => lang === key.split("_")[0]
    ) as Locale[];
    if (variants.length == 1) {
        return variants[0];
    } else if (variants.length > 1) {
        const localityVariants: Locale[] = stringLocalizationKeys.filter(
            key => locality === key.split("_")[1]
        ) as Locale[];
        if (localityVariants.length == 1) {
            return localityVariants[0];
        } else {
            console.warn(`Unable to determine locale for "${locale}"`);
        }
    }
    // undefined means default
};
export class Localize {
    static _defaultLocale?: Locale | null;
    static templates: Map<string, LocalizableString> = new Map<string, LocalizableString>();

    static addTemplates(templates: {[key: string]: LocalizableString}): void {
        Object.keys(templates).forEach(k => Localize.templates.set(k, templates[k]));
    }
    static get(key: string, fallback?: string, locale = Localize.defaultLocale): string {
        const translation = this.templates.get(key);
        if (translation?.localizations) {
            const localeValue = determineAvailableLocaleCode(locale!, translation.localizations);
            if (localeValue) {
                const translationValue = translation.localizations[localeValue];
                if (translationValue) {
                    return translationValue;
                }
            }
        }

        return translation?.default || fallback || key;
    }
    static set defaultLocale(value: Locale) {
        Localize._defaultLocale = value;
    }
    static get defaultLocale(): Locale {
        if (!Localize._defaultLocale) {
            Localize._defaultLocale = (el => {
                if (el) {
                    const [lang, locality] = el.split("-");
                    if (locality) {
                        return `${lang}_${locality.toLocaleUpperCase()}`;
                    }
                    return `${lang}_${lang.toUpperCase()}`;
                }
                return "en_US";
            })(window?.navigator.language) as Locale;
        }
        return Localize._defaultLocale;
    }
}
