import {injectable} from "inversify";
import {AdManagerBase, AdResponse} from "./index";
import {YandexSDK} from "../interfaces";
import {lazyGet, log} from "@dp-instant/core";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";

const track = (name: string, v?: number, p?: any) =>
    lazyGet<IATracker>(TypesAnalytics.tracker)?.track("admanager_" + name, v, p);

@injectable()
export class AdManagerYandex extends AdManagerBase {
    protected yasdk?: YandexSDK;
    init(yasdk: YandexSDK) {
        this.yasdk = yasdk;
    }

    async showAd(id: string, force = false, preloadNext = true): Promise<AdResponse> {
        let status = AdResponse.NOT_SUPPORTED;
        if (this.yasdk) {
            const yasdk = this.yasdk;
            status = AdResponse.UNKNOWN;
            this.onAdStarted({placement: id, status});
            status = await new Promise(resolve => {
                yasdk.adv.showFullscreenAdv({
                    callbacks: {
                        onClose: wasShown =>
                            resolve(wasShown ? AdResponse.PLAYED : AdResponse.CANCELLED),
                        onError: error => {
                            log.warn(error);
                            resolve(AdResponse.FAILED);
                        },
                        onOffline: () => resolve(AdResponse.FAILED)
                    }
                });
            });
            this.onAdEnded({placement: id, status});
            if (status === AdResponse.PLAYED) {
                track("show_inter", 1, {id});
                AdManagerBase.config.throttling.interstitial.lastShow = Date.now();
            }
        }
        return status;
    }

    async showRewardAd(id: string, preloadNext = true): Promise<AdResponse> {
        let status = AdResponse.NOT_SUPPORTED;
        if (this.yasdk) {
            const yasdk = this.yasdk;
            status = AdResponse.UNKNOWN;
            this.onAdStarted({placement: id, status});
            status = await new Promise(resolve => {
                yasdk.adv.showRewardedVideo({
                    callbacks: {
                        onClose: wasShown =>
                            resolve(wasShown ? AdResponse.PLAYED : AdResponse.CANCELLED),
                        onError: error => {
                            log.warn(error);
                            resolve(AdResponse.FAILED);
                        },
                        onRewarded: () => resolve(AdResponse.PLAYED),
                        onOffline: () => resolve(AdResponse.FAILED)
                    }
                });
            });
            this.onAdEnded({placement: id, status});
            if (status === AdResponse.PLAYED) {
                track("show_reward", 1, {id});
            }
        }
        return status;
    }
}
