import {injectable} from "inversify";
import {EventEmitter} from "eventemitter3";
import {CommonEvents, di, IGameConfigJSON, lazyGet, log, TypesCore} from "@dp-instant/core";
import {ILeaderboard, ISocial, IUser, TypesSocial} from "@dp-instant/social";
import LeaderboardEntry = FBInstant.LeaderboardEntry;
import LeaderboardPlayer = FBInstant.LeaderboardPlayer;
import {TypesApp, AppModel} from "@dp-instant/social";

@injectable()
export class LeaderboardGlobalExternal extends EventEmitter implements ILeaderboard {
    // @inject(TypesSocial.model)
    private _social: ISocial;
    private __SYNCED = false; // Temporary to save traffic

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    async sync(limit = 100): Promise<boolean> {
        if (this.__SYNCED) {
            return true;
        }
        const entries = await this.appSend("get", {friends: this.social.friends.map(f => f.id)});
        const allUsers = this.social.friends.concat(this.social.me);
        for (const id in entries) {
            const friend = this.social.getFriendById(id, allUsers);
            friend?.scores.update(undefined, this.castLeaderboardEntry(entries[id], friend));
        }
        this.__SYNCED = true;
        this.emit(CommonEvents.UPDATED);
        return true;
    }

    async submit(score: number, extData?: any): Promise<boolean> {
        const entry = {score, extData};
        if (score < this.social.me.scoreGlobal) {
            return false;
        }
        this.social.me.scores.update(undefined, this.castLeaderboardEntry(entry, this.social.me));
        await this.appSend("post", entry);
        return true;
    }

    private async appSend(method: string, body: any): Promise<any> {
        const model: AppModel | undefined = lazyGet(TypesApp.model);
        if (model) {
            const {id: gameId}: IGameConfigJSON = di.get(TypesCore.gameConfig);
            const payload = Object.assign({}, body, {
                user_id: this._social.me.id
            });
            return model.post(payload, `${gameId}/score/${method}`);
        } else {
            log.warn("Failed to send request");
        }
    }

    private castLeaderboardEntry(entry: any, userRef?: IUser): LeaderboardEntry | undefined {
        if (entry) {
            return {
                getScore: () => entry.score,
                getFormattedScore: () => "",
                getTimestamp: () => -1,
                getRank: () => 0,
                getExtraData: () => entry.extData,
                getPlayer: () => userRef?.fb as LeaderboardPlayer
            };
        }
    }

    private get social() {
        return this._social || (this._social = di.get(TypesSocial.model));
    }
}
