import {inject, injectable} from "inversify";
import {IScore, IUser, YaPlayer, ScoreType, TypesSocial} from "@dp-instant/social";
import EventEmitter from "eventemitter3";
import Player = FBInstant.Player;

@injectable()
export class UserYandex extends EventEmitter implements IUser {
    @inject(TypesSocial.userScore)
    private _scores: IScore;
    public fb: Player;
    public yaPlayer: YaPlayer;
    private _isNew = false;

    public init(source: YaPlayer, isNew = false): this {
        this.yaPlayer = source;
        this._isNew = isNew;
        return this;
    }

    public get id(): string {
        return (
            ((this.yaPlayer.getUniqueID && this.yaPlayer.getUniqueID()) ||
                (this.yaPlayer.getID && this.yaPlayer.getID())) + ""
        );
    }
    public get name(): string {
        return this.yaPlayer.getName && this.yaPlayer.getName() + "";
    }

    public get photo(): string {
        return this.yaPlayer.getPhoto && this.yaPlayer.getPhoto("large") + "";
    }

    set scoreSession(value: number) {
        this._scores.setScoreSession(value);
    }

    get scoreSession(): number {
        return this._scores.getScore(ScoreType.SESSION) || 0;
    }

    get scoreGlobal(): number {
        return this._scores.getScore(ScoreType.GLOBAL) || 0;
    }

    get scoreContext(): number {
        return this._scores.getScore(ScoreType.CONTEXT) || 0;
    }

    get scores(): IScore {
        return this._scores;
    }

    get isNew(): boolean {
        return this._isNew;
    }
}
