import {FunctionComponent, h} from "preact";
import {useState} from "preact/hooks";
import cn from "classnames";
import {Button} from "../Button/Button";

type ToggleControlProps = {
    value: boolean;
    className: string;
    onChange: (isOn: boolean) => void;
};
export const ToggleControl: FunctionComponent<ToggleControlProps> = props => {
    const [toggled, changeToggled] = useState(props.value);
    const classNames = cn({"btn--muted": toggled}, "btn-icon", `btn-icon__${props.className}`);
    return (
        <Button
            className={classNames}
            shape={"circle"}
            onClick={() => {
                changeToggled(!toggled);
                props.onChange(!toggled);
            }}
        />
    );
};
