import {injectable} from "inversify";
import {GlobalEventProvider} from "@dp-instant/core";

function invoke(callback?: () => void): void {
    if (callback) {
        callback();
    }
}

@injectable()
export abstract class BaseAction<T = never> extends GlobalEventProvider {
    onFinish?: () => void;
    onFailed?: () => void;

    protected data?: T;
    protected _resolve?: (value?: unknown) => void;
    protected _reject?: (reason?: any) => void;

    abstract execute(commandData?: T): Promise<any>;

    async run(data?: T): Promise<any> {
        this.data = data;
        try {
            await new Promise((resolve, reject) => {
                this._resolve = resolve;
                this._reject = reject;
                return this.execute(data);
            });
        } catch (e) {
            await this.failed(e);
        }

        this.finish();
    }

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    async failed(reason: any): Promise<any> {
        invoke(this.onFailed);
    }

    destroy(): void {
        delete this.data;
        delete this._resolve;
        delete this._reject;
        delete this.onFinish;
        delete this.onFailed;
    }

    protected resolve(): void {
        invoke(this._resolve);
    }

    protected reject(): void {
        invoke(this._reject);
    }

    protected finish(): void {
        invoke(this.onFinish);
    }
}
