import {injectable} from "inversify";
import {Action, lazyGet} from "@dp-instant/core";
import {TypesUI} from "@dp-instant/ui";
import {SocialFlowAction} from "./SocialFlowAction";

@injectable()
export class LevelRestartAction<T = never> extends SocialFlowAction<T> {
    TRACK_EVENT = "level_restart";

    private static LAST_RESTART: number = Date.now();

    protected SHOW_AD_INTERVAL = 10; // show ad if restarted before 10 sec

    protected needToShowAD(): boolean {
        return Date.now() - LevelRestartAction.LAST_RESTART < this.SHOW_AD_INTERVAL * 1000; // frequent restart
    }

    protected async launch(data?: T): Promise<void> {
        this.social.me.scoreSession = 0;
        await lazyGet<Action>(TypesUI.startScreenAction)?.run();
    }

    protected resolve() {
        LevelRestartAction.LAST_RESTART = Date.now();
        super.resolve();
    }
}
