import {h, FunctionComponent, JSX} from "preact";
import cn from "classnames";
import {Icon} from "../Icon/Icon";
import {IconType} from "../Icon/icons/iconType";
import {ComponentClassName} from "../../interfaces";

export const buttonTypeConst = {
    primary: "primary",
    glassy: "glassy"
} as const;

export const buttonShapeConst = {
    circle: "circle",
    oval: "oval"
} as const;

const ButtonTypes = Object.values(buttonTypeConst);
const ButtonShapes = Object.values(buttonShapeConst);

export type ButtonType = typeof ButtonTypes[number];
export type ButtonShape = typeof ButtonShapes[number];

export interface ButtonProps extends JSX.DOMAttributes<EventTarget>, ComponentClassName {
    icon?: JSX.Element | IconType;
    revertDir?: boolean;
    shape?: ButtonShape;
    htmlType?: "submit" | "button";
    type?: ButtonType;
    block?: boolean;
    bordered?: boolean;
    disabled?: boolean;
}

const getIcon = (icon: JSX.Element | IconType) => {
    if (typeof icon === "string") {
        return <Icon type={icon} />;
    }
    return icon;
};

const getClass = (type: ButtonType | ButtonShape) => {
    const obj = {...buttonTypeConst, ...buttonShapeConst};

    return `btn-${obj[type]}`;
};

export const Button: FunctionComponent<ButtonProps> = ({
    className,
    icon,
    shape,
    children,
    type,
    htmlType = "button",
    block,
    bordered,
    revertDir,
    ...props
}) => {
    const classNameType = type && getClass(type);
    const classNameShape = shape && getClass(shape);
    const classNames = cn(
        "btn",
        className,
        classNameShape,
        classNameType,
        {"btn-block": block},
        {"btn-revert": revertDir},
        {"btn-border": bordered}
    );
    const IconComponent = icon && getIcon(icon);
    return (
        <button className={classNames} type={htmlType} {...props}>
            {IconComponent}
            {children && <span className="btn-content">{children}</span>}
        </button>
    );
};
