import {h, FunctionComponent} from "preact";
import cn from "classnames";
import {Button, ButtonProps, ButtonType, Icon, Count} from "../";
import {IconType} from "../Icon/icons/iconType";

export type ClaimType = Partial<IconType>;

export interface ClaimProps extends Omit<ButtonProps, "type"> {
    type?: ClaimType;
    buttonType?: ButtonType;
    count?: string | number;
    ads?: boolean;
}

export const Claim: FunctionComponent<ClaimProps> = ({
    className,
    buttonType = "primary",
    type,
    count,
    ads,
    children,
    shape = "oval",
    ...props
}) => {
    const classNames = cn("claim", className);
    return (
        <Button className={classNames} shape={shape} type={buttonType} {...props}>
            {children}
            {type && (
                <span className="count-wrap">
                    <Icon type={type} />
                    <Count count={count} />
                </span>
            )}
            {ads && <Icon type="ads" />}
        </Button>
    );
};
