import {Action, lazyGet, log} from "@dp-instant/core";
import {inject, injectable, interfaces} from "inversify";
import {AdAction, ISocial, TypesSocial} from "@dp-instant/social";
import {TypesAds} from "@dp-instant/social";
import ServiceIdentifier = interfaces.ServiceIdentifier;
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";

@injectable()
export abstract class SocialFlowAction<T = never> extends Action<T> {
    @inject(TypesSocial.model)
    protected social: ISocial;

    @inject(TypesAds.adAction)
    protected adAction: AdAction;

    protected TRACK_EVENT: string;

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    protected abstract launch(data?: T): Promise<void>;

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    async execute(data?: T): Promise<any> {
        log.debug("#gameflow", this);
        await this.beforeLaunch();
        await this.launch(data);
        await this.afterLaunch();
        if (process.env.NODE_ENV === "development") {
            log.trace("#gameflow track", this.TRACK_EVENT);
        }
        this.resolve();
    }

    protected async beforeLaunch(): Promise<void> {
        await this.showAdIfNeeded();
    }
    protected async afterLaunch(): Promise<void> {
        if (this.TRACK_EVENT) {
            this.track();
        }
    }
    protected async showAdIfNeeded(): Promise<void> {
        if (this.needToShowAD()) {
            await this.showAd();
        }
    }
    protected sendPush(actionKey: ServiceIdentifier<any>, actionPayload?: any): void {
        this.sendPushAsync(actionKey, actionPayload).then(/* skip await */);
    }
    protected async sendPushAsync(
        actionKey: ServiceIdentifier<any>,
        actionPayload?: any
    ): Promise<void> {
        await lazyGet<Action>(actionKey)?.run(actionPayload);
    }

    protected async showAd() {
        await this.adAction.run();
    }

    protected needToShowAD(): boolean {
        return true;
    }

    protected track() {
        lazyGet<IATracker>(TypesAnalytics.tracker)?.track(
            `${this.TRACK_EVENT}${this.social.session.ftue ? "_ftue" : ""}`
        );
    }
}
