import {inject, injectable} from "inversify";
import {TypesSocial} from "../../TypesSocial";
import {FBPOPUP, ISocial} from "../../interfaces/ISocial";
import {FriendRef, IReferralRewardsModel} from "../../interfaces/IReferralRewardsModel";
import {lazyGet} from "@dp-instant/core";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";
const StatePending = "pending";
const track = (name: string, v = 1, p?: any) =>
    lazyGet<IATracker>(TypesAnalytics.tracker)?.track("social_rf_" + name, v, p);

@injectable()
export class ReferralRewardsModel<T = never> implements IReferralRewardsModel<T> {
    generateNext: (() => T) | null;
    static readonly COOKIE_KEY = "irewards";
    @inject(TypesSocial.model)
    protected social: ISocial;
    private _items: ({value: T} & FriendRef)[] = [];

    async init(defaultItemsCount: number = 3): Promise<this> {
        if (process.env.NODE_ENV === "development") {
            (window as any).v_ireward = this;
        }
        const {irewards} = await this.social.cookie.get(ReferralRewardsModel.COOKIE_KEY);
        const newFriends = this.social.friends.filter(f => f.isNew).map(f => f.id);
        this._items = irewards || [];

        if (this.generateNext) {
            const itemsToGenerate = defaultItemsCount - this._items.length;
            if (itemsToGenerate > 0) {
                this._items.push(
                    ...new Array(itemsToGenerate).fill(0).map(() => ({value: this.generateNext!()}))
                );
            }
        }
        const pendingItems = this._items.filter(reward => reward.rf === StatePending);
        while (newFriends.length) {
            const rf = newFriends.pop();
            const item = pendingItems.pop();
            if (item) {
                item.rf = rf;
            }
        }
        this.postData();
        return this;
    }
    async request(data: T): Promise<boolean> {
        const popupStatus: FBPOPUP = await this.social.invite(
            {
                filters: ["NEW_PLAYERS_ONLY"],
                maxSize: 2
            },
            true
        );
        track("request_attempt");
        if (popupStatus === FBPOPUP.ACCEPTED) {
            track("request");
            this._request(data, StatePending);
            this.postData(true);
            return true;
        }
        return false;
    }

    claim(rf?: string): boolean {
        if (rf) {
            for (let i = 0; i < this._items.length; i++) {
                if (this._items[i].rf === rf) {
                    this._items.splice(i, 1);
                    this.postData();
                    track("claim");
                    return true;
                }
            }
        }
        return false;
    }

    get items(): ({value: T} & FriendRef)[] {
        return this._items;
    }

    getPendingItems(): ({value: T} & FriendRef)[] {
        return this._items
            .filter(r => !r.rf || r.rf === StatePending)
            .sort((r1, r2) => {
                if (r1.rf === StatePending) {
                    return 1;
                } else {
                    return -1;
                }
            });
    }
    getAvailableItems(): ({value: T} & FriendRef)[] {
        return this._items.filter(r => !!r.rf && r.rf !== StatePending);
    }

    private _request(data: T, rf?: string): ({value: T} & FriendRef) | undefined {
        if (rf && rf !== StatePending) {
            for (let i = 0; i < this._items.length; i++) {
                if (this._items[i].rf === rf) {
                    return this._items[i];
                }
            }
        }
        let item = this._items.filter(i => i.value === data)[0];
        if (item) {
            item.rf = rf;
        } else {
            item = {value: data, rf};
            this._items.push(item);
        }
        this.postData();
        return item;
    }

    protected postData(force = false) {
        this.social.cookie.save(
            ReferralRewardsModel.COOKIE_KEY,
            this._items,
            force ? 0 : undefined
        );
    }
}
