import "reflect-metadata";
import {Container, decorate, injectable} from "inversify";
import EventEmitter from "eventemitter3";
import getDecorators from "inversify-inject-decorators";
import {interfaces} from "inversify";

export * from "./interfaces/IAppDefines";
export * from "./interfaces/IEventEmitter";
export * from "./interfaces/common";

export * from "./TypesCore";
export * from "./CoreModule";
export * from "./utils";
export * from "./events";
export * from "./actions";

decorate(injectable(), EventEmitter);

const di = new Container();
const {lazyInject} = getDecorators(di);
const lazyGet = <T>(id: interfaces.ServiceIdentifier<any>): T | undefined =>
    id && di.isBound(id) ? di.get(id) : undefined;

export {di, lazyInject, lazyGet};
export const CANVAS_ID = "game-canvas";
export const GAME_SCRIPT_ORIGIN =
    (() => {
        const str = Array.from(window.document.getElementsByTagName("script"))
            .filter(s => s.src.indexOf("firebaseapp.com") !== -1)
            .filter(s => s.src.indexOf("main.js"))
            .map(s => s.src)[0];

        if (str) {
            return str.substr(0, str.lastIndexOf("/") + 1);
        }
    })() || "";
