import {NotificationAction} from "./index";
import {injectable} from "inversify";
import {NotificationLocalizationsKey} from "../interfaces/NotificationLocalizations";

export interface NAFinishPayload {
    isWon: boolean;
}

@injectable()
export class NAFinish extends NotificationAction<NAFinishPayload> {
    strategy = "IMMEDIATE_CLEAR";
    template = "play_won";

    protected getI18NKey(): NotificationLocalizationsKey {
        const score = this.social.me.scoreSession;
        if (this.data?.isWon) {
            return score > 0 ? "play_won_0" : "play_won_1";
        }
        return score > 0 ? "play_won_2" : "play_won_3";
    }
}
