import {EventDispatcher, lazyGet, TypesCore} from "@dp-instant/core";
import {Inputs, useEffect} from "preact/hooks";

export const eventListenerHook = (
    eventName: string,
    handler: (...args: any[]) => void,
    inputs?: Inputs
) => {
    useEffect(() => {
        const dispatcher: EventDispatcher | undefined = lazyGet(TypesCore.dispatcher);
        dispatcher?.addListener(eventName, handler);
        return () => dispatcher?.removeListener(eventName, handler);
    }, inputs);
};
