import {FunctionComponent, h} from "preact";
import {IconType} from "./icons/iconType";
import {iconsMap} from "./icons";
import {ComponentClassName} from "../../interfaces";

export interface IconProps extends ComponentClassName {
    width?: number | string;
    height?: number | string;
    type: IconType;
}

export const Icon: FunctionComponent<IconProps> = ({type, ...props}) => {
    const IconComponent = iconsMap.get(type);
    return <IconComponent {...props} />;
};
