import {h, FunctionComponent} from "preact";
import cn from "classnames";
import {Avatar, AvatarProps} from "../Avatar/Avatar";
import {Icon, IconProps} from "../Icon/Icon";
import {ComponentClassName} from "../../interfaces";

export interface AvatarGroupProps extends ComponentClassName {}

export interface AvatarGroupItemProps extends ComponentClassName {}

const AvatarGroupItem: FunctionComponent<AvatarGroupItemProps> = ({className, ...props}) => {
    const classNames = cn("avatar-group-item", className);
    return <div className={classNames} {...props} />;
};

const AvatarGroupSeparator: FunctionComponent<IconProps> = ({className, ...props}) => {
    const classNames = cn("avatar-group-item", "avatar-group-separator", className);
    return <Icon className={classNames} {...props} />;
};

const AvatarGroupAvatar: FunctionComponent<AvatarProps> = props => <Avatar {...props} />;

export const AvatarGroup: FunctionComponent<AvatarGroupProps> & {
    Item: typeof AvatarGroupItem;
    Separator: typeof AvatarGroupSeparator;
    Avatar: typeof AvatarGroupAvatar;
} = ({className, ...props}) => {
    const classNames = cn("avatar-group", className);
    return <div className={classNames} {...props} />;
};

AvatarGroup.Item = AvatarGroupItem;
AvatarGroup.Separator = AvatarGroupSeparator;
AvatarGroup.Avatar = AvatarGroupAvatar;
