import {ISocial, IUser, SocialPlatformProvider, TypesSocial} from "@dp-instant/social";
import {injectable} from "inversify";
import {di, log} from "@dp-instant/core";
import {CookieDataYandex} from "../data/CookieDataYandex";
import {ISocialInitParams, YandexSDK, YaPlayer} from "../interfaces";
import {PaymentsModelYandex} from "./PaymentsModelYandex";
import {SocialModelBase} from "./SocialModelBase";
import {UserYandex} from "./user/UserYandex";

@injectable()
export class SocialModelYandex extends SocialModelBase implements ISocial {
    readonly payments: PaymentsModelYandex;
    readonly cookie: CookieDataYandex;
    private _yasdk: YandexSDK;
    private _me: IUser;
    private _userAuthorized = false;

    async init(params: {yasdk?: YandexSDK} & ISocialInitParams): Promise<void> {
        const {yasdk} = params;
        this._yasdk = yasdk!;
        await this.updateUserData(false);
        await this.payments.init({yasdk: this._yasdk});
        return super.init(params);
    }
    get me(): IUser {
        return this._me;
    }

    async updateUserData(scopes = false): Promise<void> {
        let player: YaPlayer | undefined;
        try {
            player = await this._yasdk.getPlayer({scopes});
            if (scopes) {
                this._userAuthorized = true;
            }
        } catch (e) {
            log.warn(e);
        }

        this._me ||= di.get<UserYandex>(TypesSocial.user);
        (this._me as UserYandex).init(player || di.get(TypesSocial.dummyUser));
        this.cookie.init(player);
    }
    async authorizeUser(): Promise<void> {
        if (!this._userAuthorized) {
            try {
                await this._yasdk.auth.openAuthDialog();
                await this.updateUserData(true);
            } catch (e) {
                log.warn(e);
            }
        }
    }
    postSessionScore(score?: number): void {
        // FIXME: temporary solution for Chain Cube
        this.authorizeUser();
    }

    get socialPlatform(): SocialPlatformProvider {
        return "ya";
    }
}
