import {injectable} from "inversify";
import {TypesUI, UIEvents} from "@dp-instant/ui";
import {Action, di} from "@dp-instant/core";

@injectable()
export class StartScreenAction extends Action {
    public async execute() {
        if (di.isBound(TypesUI.screen.HOME)) {
            this.dispatch(UIEvents.SCREEN_CHANGED, {id: TypesUI.screen.HOME});
        } else {
            this.dispatch(UIEvents.SCREEN_CHANGED, {id: TypesUI.screen.GAMEPLAY});
        }
        this.resolve();
    }
}
