import {IATracker, TypesAnalytics} from "@dp-instant/analytics";
import {lazyGet, log} from "@dp-instant/core";
import {injectable} from "inversify";
import {
    IPaymentsModel,
    IPaymentsModelConfig,
    YandexSDK,
    YaPaymentsAPI,
    YaProduct,
    YaPurchase
} from "../interfaces";

const urlRequest = async (url: string, payload: any) => {
    try {
        const rawResponse = await fetch(url, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
            },
            body: JSON.stringify(payload)
        });
        return await rawResponse.json();
    } catch (e) {
        log.warn(e);
    }
    return {};
};
