const track = (eventName: string, params?: any) =>
    lazyGet<IATracker>(TypesAnalytics.tracker)?.track("purchase_" + eventName, 1, params);

interface IPaymentsModelConfigYandex extends IPaymentsModelConfig {
    yasdk?: YandexSDK;
}
@injectable()
export class PaymentsModelYandex implements IPaymentsModel<YaProduct> {
    private _config?: IPaymentsModelConfigYandex;
    private _catalog?: YaProduct[];
    private _purchases?: YaPurchase[];
    private __initPendingPromise?: Promise<void>;
    private _yasdk: YandexSDK;
    private _paymentsSDK: YaPaymentsAPI;
    private _supported = false;

    async init(config?: IPaymentsModelConfigYandex): Promise<any> {
        this._yasdk = config?.yasdk!;
        this._config = config;
        this.__initPendingPromise ||= (async () => {
            if (this._yasdk) {
                try {
                    this._paymentsSDK = await this._yasdk.getPayments({signed: true});
                } catch (e) {
                    log.warn(e);
                    return;
                }
                this._supported = true;
                if (this._supported) {
                    await this.updatePurchasesAndCatalog();
                    delete this.__initPendingPromise;
                }
            }
        })();

        return this.__initPendingPromise;
    }
    private async updatePurchasesAndCatalog() {
        const [catalog, purchases] = await Promise.all([
            this.fetchCatalog(),
            this.fetchPurchases()
        ]);
        this._catalog = catalog;
        this._purchases = purchases;
    }
    private fetchCatalog() {
        return this._paymentsSDK.getCatalog();
    }
    private fetchPurchases() {
        return this._paymentsSDK.getPurchases();
    }

    getById(id: string): YaProduct | undefined {
        return this._catalog?.filter(p => p.id == id)[0];
    }
    getAll(): YaProduct[] {
        return this._catalog || [];
    }
    isPaid(id: string): boolean {
        return !!this._purchases?.filter(p => p.productID == id)[0];
    }
    async consume(token: string): Promise<void> {
        track("consume_attempt", {token});
        try {
            await this._paymentsSDK.consumePurchase(token);
        } catch (e) {
            log.warn("consumePurchaseAsync", e);
            track("consume_failed", {error: e.code || "unknown", token});
            return;
        }
        track("consume_success", {token});
        await this.updatePurchasesAndCatalog();
    }
    async purchase(id: string): Promise<boolean> {
        const product = this.getById(id);
        if (!product) {
            log.error(`Purchase failed. No product with id ${id}`);
            return false;
        }

        if (this.isPaid(id)) {
            log.info(`Product ${id} is already paid`);
            return true;
        }
        track("attempt", {id});
        const purchaseMade = await (async () => {
            if (product) {
                const {id: productID} = product;
                if (productID) {
                    let purchase;
                    try {
                        purchase = await this._paymentsSDK.purchase({id: productID});
                    } catch (e) {
                        track("attempt_failed", {error: e.code || "unknown", id});
                        log.error(e);
                        // TODO: handle errors
                        // code: "USER_INPUT"
                        // message: "User cancelled payment"
                    }
                    return purchase;
                }
            }
        })();

        if (purchaseMade) {
            track("made", {id});
            const success = await this.validate(purchaseMade);
            if (success) {
                await this.updatePurchasesAndCatalog();
                return success;
            }
        }
        return false;
    }
    async validate(purchaseMade: any): Promise<boolean> {
        let status = true;
        // if (this._config?.payments_backend_url) {
        //     try {
        //         const response = await urlRequest(this._config.payments_backend_url, purchaseMade);
        //         track("validate", response.success);
        //         status = !!response.success;
        //     } catch (e) {
        //         Sentry.captureException(e);
        //     }
        // } else {
        //     log.warn("Backend validation is not configured");
        // }
        return status;
    }
    get purchases(): YaPurchase[] | undefined {
        return this._purchases;
    }
    get supported(): boolean {
        return this._supported;
    }
}
