import {ContainerModule} from "inversify";
import {interfaces} from "inversify";
import {TypesCore} from "./TypesCore";
import {EventDispatcher, GlobalEventProvider} from "./events";
import {IGameConfigJSON} from "./interfaces/IAppDefines";
import {IEventEmitter} from "./interfaces/IEventEmitter";

export const CoreModule = new ContainerModule((
    bind: interfaces.Bind /*, unbind, isBound, rebind*/
) => {
    bind<IGameConfigJSON>(TypesCore.gameConfig).toConstantValue({id: "unset"});
    bind<IEventEmitter>(TypesCore.dispatcher).to(EventDispatcher).inSingletonScope();
    bind(TypesCore.eventProvider).to(GlobalEventProvider);
});
