import "./CrossPromo.less";
import {di, GAME_SCRIPT_ORIGIN, lazyInject} from "@dp-instant/core";
import {ISocial, TypesSocial} from "@dp-instant/social";
import {Component, ComponentChild, h} from "preact";
import {CrossPromoIconConf, TypesPromo} from "@dp-instant/crosspromo";

export class CrossPromo extends Component<any, any> {
    @lazyInject(TypesSocial.model)
    protected social: ISocial;

    @lazyInject(TypesPromo.random)
    protected promoConf: CrossPromoIconConf;

    render(): ComponentChild {
        if (this.isFeatureAvailable()) {
            return (
                <div className={`crosspromo${this.visibilityClass}`} onClick={() => this.onClick()}>
                    <img
                        className={"crosspromo__icon"}
                        src={`${GAME_SCRIPT_ORIGIN}crosspromo/${this.promoConf.icon}`}
                    />
                    <div className={"crosspromo__description"}>
                        <div className={"title"}>{this.promoConf.title}</div>
                        <div className={"subtitle"}>{this.promoConf.subtitle}</div>
                    </div>
                </div>
            );
        }
    }
    public componentDidMount(): void {
        setTimeout(() => this.setState({visible: true}), 1);
    }
    protected isFeatureAvailable() {
        if (typeof FBInstant !== "undefined") {
            return (
                FBInstant.getSupportedAPIs().indexOf("switchGameAsync") !== -1 &&
                di.isBound(TypesPromo.config)
            );
        }
    }
    protected async onClick() {
        await this.social.switchGame(this.promoConf.appId, this.promoConf.data);
    }
    protected get visibilityClass(): string {
        return this.state.visible ? " visible" : "";
    }
}
