import ContextOptions = FBInstant.ContextOptions;
import CustomUpdatePayload = FBInstant.CustomUpdatePayload;
import LeaderboardUpdatePayload = FBInstant.LeaderboardUpdatePayload;
import {IUser, SocialPlatformProvider} from "@dp-instant/social";
import {IEventEmitter} from "@dp-instant/core";
import {SessionData} from "../data/SessionData";
import {IPaymentsModel} from "./IPaymentsModel";
import {ICookieData} from "../data/ICookieData";

export enum FBPOPUP {
    ACCEPTED,
    CANCELLED,
    REJECTED,
    FAILED
}
export enum SocialEvents {
    CONTEXT_CHANGE = "context_change",
    SHOW_OVERLAY = "show_social_overlay",
    HIDE_OVERLAY = "hide_social_overlay"
}
export type ISocialInitParams = {cookies?: string[]};
export interface ISocial extends IEventEmitter {
    readonly me: IUser;
    readonly cookie: ICookieData;
    readonly session: SessionData;
    readonly payments: IPaymentsModel;
    readonly opponent: IUser | undefined;
    readonly nobody: IUser;
    readonly contextPlayers: IUser[];
    readonly inBlindContext: boolean;
    readonly inSolo: boolean;
    readonly inGroup: boolean;
    readonly friends: IUser[];
    readonly socialPlatform: SocialPlatformProvider;

    getFriendById(id?: string, arr?: any[]): IUser | undefined;

    init(params?: ISocialInitParams): Promise<void>;

    playSolo(): Promise<void>;

    playOffline(tag?: string): Promise<FBPOPUP>;

    playWith(idOrOpponent?: string | IUser, instantResolve?: boolean): Promise<FBPOPUP>;

    showShortcutPopup(): Promise<boolean>;

    startGameAsync(): Promise<void>;

    showBotPopup(ref?: string): Promise<boolean>;

    share(payload: FBInstant.SharePayload): Promise<boolean>;

    notify(payload: CustomUpdatePayload | LeaderboardUpdatePayload): Promise<boolean>;

    switchGame(id: string, data?: any): Promise<void>;

    invite(options?: ContextOptions, instantResolve?: boolean): Promise<FBPOPUP>;

    getRandomChallenger(ignoreUsers?: IUser[]): IUser | undefined;

    getRandomOpponent(): IUser | undefined;

    syncLeaderboards(): Promise<void>;

    /**
     *
     * @param score – social.me.scoreSession used by default
     */
    postSessionScore(score?: number): void;
}
