import {Action, CANVAS_ID, di} from "@dp-instant/core";
import {TypesUI} from "@dp-instant/ui";
import {injectable} from "inversify";
import {h, render} from "preact";

type ParentNode = Element | Document | ShadowRoot | DocumentFragment;

@injectable()
export class SetupUIAction extends Action<ParentNode | undefined> {
    async execute(node?: ParentNode): Promise<any> {
        render(
            h(di.get(TypesUI.uiRootClass), {ref: (ref: any) => this.onRefCreated(ref)}),
            node || document.getElementById(CANVAS_ID)!.parentElement!
        );
        this.resolve();
    }
    protected onRefCreated(ref: any) {
        di.bind(TypesUI.root).toConstantValue(ref);
        if (process.env.NODE_ENV === "development") {
            (window as any).v_ui = ref;
        }
    }
}
