import {h, FunctionComponent} from "preact";
import cn from "classnames";
import {Avatar, AvatarProps} from "../Avatar/Avatar";
import {Score} from "../Score/Score";
import {ComponentClassName} from "../../interfaces";

export interface AvatarPlayInGroupProps extends ComponentClassName {
    score?: string;
}

export interface AvatarPlayInGroupAvatarProps extends Omit<AvatarProps, "playWithBtn" | "score"> {
    className?: string;
}

const AvatarGroupItem: FunctionComponent<AvatarPlayInGroupAvatarProps> = ({
    className,
    ...props
}) => {
    const classNames = cn("avatar-play-in-group-item", className);
    return (
        <li className={classNames}>
            <Avatar {...props} />
        </li>
    );
};

export const AvatarPlayInGroup: FunctionComponent<AvatarPlayInGroupProps> & {
    Avatar: typeof AvatarGroupItem;
} = ({className, score, children}) => {
    const classNames = cn("avatar-play-in-group", className);
    return (
        <span className={classNames}>
            <ul className="avatar-play-in-group-inner">{children}</ul>
            {score && <Score className="avatar-score">{score}</Score>}
        </span>
    );
};

AvatarPlayInGroup.Avatar = AvatarGroupItem;
