import {lazyGet} from "@dp-instant/core";
import {UIEvents} from "@dp-instant/ui";
import {ComponentChild, ComponentType, h} from "preact";
import {GlobalEventProviderComponent} from "./GlobalEventProviderComponent";

type PopupsState = {
    Popup: any;
    PopupProps: any;
};
export class Popups extends GlobalEventProviderComponent<any, PopupsState> {
    state = {Popup: null, PopupProps: null};
    public componentWillMount() {
        this.addListener(UIEvents.POPUP, this.onScreenChanged);
    }
    public onScreenChanged(event: any) {
        const {id, props: PopupProps} = event;
        this.setState({
            ...this.state,
            Popup: lazyGet(id),
            PopupProps
        });
    }

    render(props: any, state: PopupsState): ComponentChild {
        const Screen = state.Popup as ComponentType;
        if (Screen) {
            return h(Screen, state.PopupProps);
        }
    }
}
