export type ISessionCookies = {gameSession: number; friends: string[]};

export class SessionData {
    private data: ISessionCookies = {
        gameSession: NaN,
        friends: []
    };

    init(session: any): ISessionCookies {
        this.data.gameSession = (parseInt(session?.gameSession, 10) || 0) + 1;
        this.data.friends = session?.friends || [];
        return this.getRawData();
    }

    getRawData(): ISessionCookies {
        return this.data;
    }

    get ftue(): boolean {
        return this.data.gameSession <= 1;
    }
}
