import {injectable} from "inversify";
import {log} from "@dp-instant/core";
import {ICookieData} from "./ICookieData";
import {YaPlayer} from "../interfaces";
import {CookieDataBase} from "./CookieDataBase";

@injectable()
export class CookieDataYandex extends CookieDataBase implements ICookieData {
    protected _player?: YaPlayer;

    init(player?: YaPlayer) {
        this._player = player;
    }

    async uploadToRemote(keys?: string[]): Promise<void> {
        const obj: {[key: string]: {}} = {};
        this.cache.forEach((v, k) => {
            if (!keys || keys.includes(k)) {
                obj[k] = v;
            }
        });
        log.debug(this, "setDataAsync", obj);
        if (this._player && this._player.setData) {
            await this._player.setData(obj);
        } else {
            log.error("CookieDataBase::uploadToRemote. Not supported");
        }
    }

    protected async downloadFromRemote(keys?: string[]) {
        const allKeys = Array.from(this.cache.keys());
        if (this._player && this._player.getData) {
            const rawData: any = await this._player.getData(
                isNaN(this.lastSync) ? allKeys : keys || allKeys
            );
            log.debug(this, "getDataAsync", rawData);
            if (rawData) {
                Object.keys(rawData).forEach(i => {
                    this.cache.set(i, rawData[i]);
                });
            }
            this.lastSync = Date.now();
        } else {
            log.error("CookieDataBase::downloadFromRemote. Not supported");
        }
    }
}
