import {IAProvider} from "@dp-instant/analytics";
import {injectable} from "inversify";
import firebase from "firebase/app";
import App = firebase.app.App;
import Analytics = firebase.analytics.Analytics;

@injectable()
export class FirebaseAnalyticsProvider implements IAProvider {
    protected firebaseAnalytics: Analytics;
    init(firebaseApp: App) {
        if (firebase.analytics.isSupported()) {
            this.firebaseAnalytics = firebase.analytics(firebaseApp);
        }
    }
    track(name: string, value: number, params: any): void {
        if (this.firebaseAnalytics) {
            const eventParams = {value, ...params};
            this.firebaseAnalytics.logEvent(name, eventParams);
            // log.trace("firebaseAnalytics", name, eventParams);
        }
    }
}
