import {h} from "preact";
import {Graphics} from "../../Graphics/Graphics";
// @ts-ignore
import * as ads_svg from "../../../svg/ads.svg";
// @ts-ignore
import * as friends_svg from "../../../svg/friends.svg";
// @ts-ignore
import * as globe_svg from "../../../svg/globe.svg";
// @ts-ignore
import * as play_svg from "../../../svg/play.svg";
// @ts-ignore
import * as replay_svg from "../../../svg/replay.svg";
// @ts-ignore
import * as leaderboard_svg from "../../../svg/leaderboard.svg";
// @ts-ignore
import * as x_svg from "../../../svg/x.svg";
// @ts-ignore
import * as share_svg from "../../../svg/share.svg";
// @ts-ignore
import * as trophy_svg from "../../../svg/trophy.svg";
// @ts-ignore
import * as placeholder_avatar_svg from "../../../svg/avatar-bg.svg";
// @ts-ignore
import * as bomb_svg from "../../../svg/bomb.svg";
// @ts-ignore
import * as heart_svg from "../../../svg/heart.svg";
// @ts-ignore
import * as vs_svg from "../../../svg/vs.svg";
// @ts-ignore
import * as gear_svg from "../../../svg/gear.svg";
import {IconType} from "./iconType";

const construct = (map: Map<string, any>) => {
    const resultMap: Map<string, any> = new Map();
    map.forEach((svgFile, id) =>
        resultMap.set(id, (props: any) => <Graphics svg={svgFile} {...props} />)
    );
    return resultMap;
};
export const iconsMap = construct(
    new Map<IconType, any>([
        ["ads", ads_svg],
        ["friends", friends_svg],
        ["globe", globe_svg],
        ["play", play_svg],
        ["replay", replay_svg],
        ["leaderboard", leaderboard_svg],
        ["share", share_svg],
        ["x", x_svg],
        ["trophy", trophy_svg],
        ["bomb", bomb_svg],
        ["vs", vs_svg],
        ["heart", heart_svg],
        ["placeholder-avatar", placeholder_avatar_svg],
        ["gear", gear_svg]
    ])
);
