import {ContainerModule} from "inversify";
import {interfaces} from "inversify";
import {TypesAudio} from "./TypesAudio";
import {AudioModel} from "./model/AudioModel";
import {PlayMusicAction} from "./actions/PlayMusicAction";
import {PlaySoundAction} from "./actions/PlaySoundAction";

export const AudioModule = new ContainerModule((
    bind: interfaces.Bind /*, unbind, isBound, rebind*/
) => {
    bind(TypesAudio.model).to(AudioModel).inSingletonScope();
    bind(TypesAudio.soundAction).to(PlaySoundAction);
    bind(TypesAudio.musicAction).to(PlayMusicAction);
});
