import {ContainerModule, interfaces} from "inversify";
import {
    CheatsAction,
    LevelEndAction,
    LevelNextAction,
    LevelRestartAction,
    LevelStartAction,
    PlayWithOpponentAction
} from "./actions";
import {TypesFlow} from "./TypesFlow";

export const GameFlowModule = new ContainerModule((
    bind: interfaces.Bind /*, unbind, isBound, rebind*/
) => {
    bind(TypesFlow.LevelStart).to(LevelStartAction);
    bind(TypesFlow.LevelEnd).to(LevelEndAction);
    bind(TypesFlow.LevelNext).to(LevelNextAction);
    bind(TypesFlow.LevelRestart).to(LevelRestartAction);
    bind(TypesFlow.PlayWith).to(PlayWithOpponentAction);

    if (process.env.NODE_ENV === "development") {
        bind("flow.cheats").to(CheatsAction);
    }
});
