import AdInstance = FBInstant.AdInstance;
import {GlobalEventProvider, lazyGet, log} from "@dp-instant/core";
import {injectable} from "inversify";
import {AdEvents, AdResponse} from "./index";
import {AdEvent} from "./AdEvent";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";
import {AdManagerBase} from "./AdManagerBase";

const track = (name: string, v?: number, p?: any) =>
    lazyGet<IATracker>(TypesAnalytics.tracker)?.track("admanager_" + name, v, p);
const AKeys = {
    SHOW_BANNER: "show_banner",
    SHOW_INTER: "show_inter",
    SHOW_REWARD: "show_reward"
};

@injectable()
export class AdManagerFacebook extends AdManagerBase {
    private _pendingInterstitialInstance?: Promise<AdInstance | undefined>;
    private _pendingRewardInstance?: Promise<AdInstance | undefined>;
    private inters: AdInstance[] = [];
    private _bannerIsShowing = false;
    // private rewards: AdInstance[] = [];
    private rewards: {[placement: string]: AdInstance[]} = {};

    private isSupported(required: string | string[]) {
        required = typeof required === "string" ? [required] : required;
        const apis = FBInstant.getSupportedAPIs();
        for (const api of required) {
            if (!apis.includes(api)) {
                log.warn(`"${api}" is not supported`);
                return false;
            }
        }
        return true;
    }

    private async getInterstitialInstance(
        placementID: string,
        force = false
    ): Promise<AdInstance | undefined> {
        let instance: AdInstance | undefined;
        log.debug("request ad", placementID);
        if (!force) {
            while (this.inters.length) {
                const preload = this.inters.shift();
                if (preload) {
                    return preload;
                }
            }
        }
        if (this._pendingInterstitialInstance) {
            log.debug("pending", placementID);
            return this._pendingInterstitialInstance;
        }
        return (this._pendingInterstitialInstance = (async () => {
            if (placementID === "") {
                log.warn("skip empty placement"); // otherwise facebook choke
                return;
            }
            try {
                instance = await FBInstant.getInterstitialAdAsync(placementID);
            } catch (err) {
                log.error("getInterstitialAdAsync failed", err);
            }
            if (instance) {
                log.debug("got ad", instance.getPlacementID());
                try {
                    await instance.loadAsync();
                } catch (err) {
                    log.error("interstitial loadAsync failed", err);
                }
            }
            delete this._pendingInterstitialInstance;

            return instance;
        })());
    }

    private async getRewardInstance(
        placementID: string,
        force = false
    ): Promise<AdInstance | undefined> {
        let instance: AdInstance | undefined;
        log.debug("request reward ad", placementID);
        if (!force) {
            if (this.rewards[placementID]) {
                while (this.rewards[placementID].length) {
                    const preloaded = this.rewards[placementID].shift();
                    if (preloaded) {
                        return preloaded;
                    }
                }
            }
        }
        if (this._pendingRewardInstance) {
            log.debug("pending", placementID);
            return this._pendingRewardInstance;
        }
        return (this._pendingRewardInstance = (async () => {
            if (placementID === "") {
                log.warn("skip empty placement"); // otherwise facebook choke
                return;
            }
            try {
                instance = await FBInstant.getRewardedVideoAsync(placementID);
            } catch (err) {
                log.error("getRewardedVideoAsync failed", err);
            }
            if (instance) {
                log.debug("got ad", instance.getPlacementID());
                try {
                    await instance.loadAsync();
                } catch (err) {
                    log.error("interstitial loadAsync failed", err);
                }
            }
            delete this._pendingRewardInstance;
            return instance;
        })());
    }

    public async preloadNextInter(placementID: string): Promise<void> {
        if (!AdManagerBase.config.interstitialsDisabled) {
            const nextAd = await this.getInterstitialInstance(placementID, true);
            if (nextAd) {
                this.inters.push(nextAd);
            }
        }
    }

    public async preloadNextReward(placementID: string): Promise<void> {
        const nextAd = await this.getRewardInstance(placementID /*, true*/);
        if (nextAd) {
            this.rewards[placementID] = this.rewards[placementID] || [];
            this.rewards[placementID].push(nextAd);
        }
    }

    async showBanner(id: string = AdManagerBase.config.ids.BANNER): Promise<AdResponse> {
        let status = AdResponse.UNKNOWN;
        if (this.isSupported("loadBannerAdAsync")) {
            try {
                //@ts-ignore
                await FBInstant.loadBannerAdAsync(id);
                status = AdResponse.PLAYED;
            } catch (e) {
                status = AdResponse.FAILED;
            }
        }
        if (status === AdResponse.PLAYED) {
            track(AKeys.SHOW_BANNER, 1, {id});
            this.onBannerShown();
        } else {
            this.onBannerHide();
        }
        return status;
    }
    async hideBanner(): Promise<boolean> {
        let hidden = false;
        if (this.isSupported("hideBannerAdAsync")) {
            try {
                //@ts-ignore
                await FBInstant.hideBannerAdAsync();
                hidden = true;
            } catch (e) {}
            hidden && this.onBannerHide();
        }
        return hidden;
    }
    async showAd(id: string, force = false, preloadNext = true): Promise<AdResponse> {
        let status = AdResponse.UNKNOWN;
        if (AdManagerBase.config.interstitialsDisabled) {
            return status;
        }
        if (this.isSupported("getInterstitialAdAsync")) {
            if (!force) {
                if (AdManagerBase.needToThrottleAd(AdManagerBase.config.throttling.interstitial)) {
                    log.trace("ad throttled");
                    return AdResponse.THROTTLED;
                }
            }
            this.onAdStarted({placement: id, status});
            const instance = await this.getInterstitialInstance(id);
            if (instance) {
                try {
                    await instance.showAsync();
                    status = AdResponse.PLAYED;
                } catch (e) {
                    status = AdResponse.FAILED;
                    log.warn("interstitial show async failed", e);
                }
            } else {
                status = AdResponse.NO_FILL;
                log.warn("no ad instance");
            }
            this.onAdEnded({placement: id, status});

            if (status === AdResponse.PLAYED) {
                track(AKeys.SHOW_INTER, 1, {id});
                AdManagerBase.config.throttling.interstitial.lastShow = Date.now();
                if (preloadNext) {
                    this.preloadNextInter(id);
                }
            }
        }
        return status;
    }

    async showRewardAd(id: string, preloadNext = true): Promise<AdResponse> {
        let status = AdResponse.UNKNOWN;
        if (!this.isSupported("getRewardedVideoAsync")) {
            return status;
        }
        this.onAdStarted({placement: id, reward: true, status});
        const instance = await this.getRewardInstance(id);
        if (instance) {
            try {
                await instance.showAsync();
                status = AdResponse.PLAYED;
            } catch (e) {
                status = AdResponse.FAILED;
                if (e.code === "USER_INPUT") {
                    status = AdResponse.CANCELLED;
                }
                log.warn("reward show async failed", e);
            }
        } else {
            status = AdResponse.NO_FILL;
            log.warn("no reward ad instance");
        }
        this.onAdEnded({placement: id, reward: true, status});

        if (status === AdResponse.PLAYED) {
            track(AKeys.SHOW_REWARD, 1, {id});
            if (preloadNext) {
                this.preloadNextReward(id);
            }
        }
        return status;
    }
    get bannerIsShowing(): boolean {
        return this._bannerIsShowing;
    }
    protected onAdStarted(paload: AdEvent) {
        this.dispatch(AdEvents.STARTED, paload);
    }
    protected onAdEnded(paload: AdEvent) {
        this.dispatch(AdEvents.ENDED, paload);
    }
    protected onBannerShown() {
        this._bannerIsShowing = true;
        this.dispatch(AdEvents.BANNER_SHOWN);
    }
    protected onBannerHide() {
        this._bannerIsShowing = false;
        this.dispatch(AdEvents.BANNER_HIDDEN);
    }
}
