import {FBPOPUP, IUser} from "@dp-instant/social";
import {LevelStartAction, TypesFlow} from "@dp-instant/gameflow";
import {inject, injectable} from "inversify";
import {SocialFlowAction} from "./SocialFlowAction";

@injectable()
export class LevelNextAction extends SocialFlowAction<IUser | undefined> {
    TRACK_EVENT = "level_next";

    static FREE_SOLO_ATTEMPTS = Number.MAX_SAFE_INTEGER;

    @inject(TypesFlow.LevelStart)
    protected levelStart: LevelStartAction<any>;

    constructor() {
        super();
        this.onFailed = () => {
            throw new Error("Something went wrong");
        };
    }
    async launch(nextOpponent?: IUser): Promise<void> {
        if (!nextOpponent) {
            LevelNextAction.FREE_SOLO_ATTEMPTS--;
            return this.playNextSolo();
        }
        if (nextOpponent === this.social.opponent) {
            return this.gotoNextLevel();
        } else {
            switch (await this.social.playWith(nextOpponent, true)) {
                case FBPOPUP.ACCEPTED:
                    return this.gotoNextLevel(true);
                case FBPOPUP.CANCELLED:
                    if (LevelNextAction.FREE_SOLO_ATTEMPTS > 0) {
                        LevelNextAction.FREE_SOLO_ATTEMPTS--;
                        return this.playNextSolo();
                    }
            }
        }
    }

    protected async playNextSolo() {
        await Promise.all([this.social.playSolo(), this.gotoNextLevel()]);
    }
    protected async gotoNextLevel(waitForContextChange = false) {
        this.levelStart.waitForContextChange = waitForContextChange;
        await this.levelStart.run();
    }

    protected needToShowAD(): boolean {
        return false;
    }
}
