import LeaderboardEntry = FBInstant.LeaderboardEntry;
import {EventEmitter} from "eventemitter3";
import {IScore, ScoreEvent, ScoreType} from "@dp-instant/social";

export class UserScore extends EventEmitter implements IScore {
    private _contextScore: LeaderboardEntry | undefined;
    private _globalScore: LeaderboardEntry | undefined;
    private _sessionScore: number | undefined;

    setScoreSession(value: number) {
        if (isNaN(value)) {
            this._sessionScore = NaN;
        } else {
            const prev = this._sessionScore || 0;
            this._sessionScore = value;
            if (value >= prev) {
                this.emit(ScoreEvent.INCREASED);
            }
            this.emit(ScoreEvent.UPDATED);
        }
    }

    update(context?: LeaderboardEntry, global?: LeaderboardEntry) {
        this._contextScore = context || this._contextScore;
        this._globalScore = global || this._globalScore;
        if (global || context) {
            this.emit(ScoreEvent.UPDATED);
        }
    }
    getScore(
        mask: ScoreType | number = ScoreType.SESSION | ScoreType.CONTEXT | ScoreType.GLOBAL
    ): number | undefined {
        const hasFlag = (flag: number, msk: number): boolean => !!(msk & flag);

        let scoresToCheck: (number | undefined)[] = [];
        if (hasFlag(ScoreType.SESSION, mask)) {
            scoresToCheck.push(this._sessionScore);
        }
        if (hasFlag(ScoreType.CONTEXT, mask)) {
            scoresToCheck.push(this._contextScore && this._contextScore.getScore());
        }
        if (hasFlag(ScoreType.GLOBAL, mask)) {
            scoresToCheck.push(this._globalScore && this._globalScore.getScore());
        }
        scoresToCheck = scoresToCheck.filter(s => !isNaN(s as number)) as number[];
        if (scoresToCheck.length) {
            return Math.max.apply(null, scoresToCheck as number[]);
        }
    }
    getEntry(type: ScoreType): LeaderboardEntry | undefined {
        switch (type) {
            case ScoreType.GLOBAL:
                return this._globalScore;
            case ScoreType.CONTEXT:
                return this._contextScore;
        }
    }
    flush(): void {
        delete this._contextScore;
        // delete this._globalScore; FIXME: bound to External leaderboard single sync optimization
        delete this._sessionScore;
    }
}
