import {di, EventDispatcher, TypesCore} from "@dp-instant/core";
import {Inputs, useLayoutEffect} from "preact/hooks";

export const useEventListenerEffect = (
    eventName: string,
    handler: (...args: any[]) => void,
    inputs?: Inputs
) => {
    useLayoutEffect(() => {
        const dispatcher: EventDispatcher = di.get(TypesCore.dispatcher);
        dispatcher.addListener(eventName, handler);
        return () => dispatcher.removeListener(eventName, handler);
    }, inputs);
};
