import {Action, lazyGet} from "@dp-instant/core";
import {injectable} from "inversify";
import {Howl} from "howler";
import {TypesAudio} from "../TypesAudio";

export type PlayMusicActionPayload = {
    active: boolean;
    sprite?: string;
    howlInstance?: Howl;
};

@injectable()
export class PlayMusicAction extends Action<PlayMusicActionPayload> {
    private static activeMusicState: {playing: boolean; instance?: number} = {playing: false};

    async execute(payload: PlayMusicActionPayload): Promise<void> {
        const {active, sprite, howlInstance} = payload;
        const howl = howlInstance || lazyGet<Howl>(TypesAudio.musicHowl);

        if (!howl) {
            return;
        }
        const hasMusicInstance = !isNaN(<number>PlayMusicAction.activeMusicState.instance);
        if (active) {
            if (hasMusicInstance) {
                if (!PlayMusicAction.activeMusicState.playing) {
                    PlayMusicAction.activeMusicState.playing = true;
                    this.fadeTo(howl, 1, <number>PlayMusicAction.activeMusicState.instance);
                }
            } else {
                PlayMusicAction.activeMusicState.instance = howl.play(sprite || "track") || NaN;
                PlayMusicAction.activeMusicState.playing = true;
            }
        } else {
            if (PlayMusicAction.activeMusicState.playing) {
                PlayMusicAction.activeMusicState.playing = false;
                if (hasMusicInstance) {
                    this.fadeTo(howl, 0, <number>PlayMusicAction.activeMusicState.instance);
                }
            }
        }
    }
    protected fadeTo(howl: Howl, to: number, trackId: number, duration = 500) {
        const from = howl.volume();
        duration *= Math.abs(Math.min(from, to) - Math.max(from, to));
        howl.fade(from, to, duration, trackId);
    }
}
