import {injectable} from "inversify";
import {di, log, TypesCore} from "@dp-instant/core";
import {IAProvider, IATracker} from "./index";
import firebase from "firebase/app";
import "firebase/analytics";
import "firebase/remote-config";
import Analytics = firebase.analytics.Analytics;

@injectable()
export class AnalyticsTracker implements IATracker {
    protected providers: Set<IAProvider> = new Set<IAProvider>();
    private firebaseAnalytics?: Analytics;
    private _useFb: boolean = true;
    init() {
        const {analytics: analyticsConfig} = di.get(TypesCore.gameConfig);
        if (analyticsConfig) {
            const {fb, firebase: firebaseConf} = analyticsConfig;
            if (firebaseConf) {
                // const app = firebase.initializeApp(firebaseConf);
                // this.firebaseAnalytics = firebase.analytics(app);
            }
            if (typeof fb === "boolean") {
                this._useFb = fb;
            }
        }
    }
    addProvider(tracker: IAProvider) {
        this.providers.add(tracker);
    }
    removeProvider(tracker: IAProvider) {
        this.providers.delete(tracker);
    }
    track(...params: any) {
        let [name, value, p] = params;
        if (name) {
            this.providers.forEach(p => p.track(name, value || 0, p || {}));
        }
    }
}
