import Leaderboard = FBInstant.Leaderboard;
import LeaderboardEntry = FBInstant.LeaderboardEntry;
import {injectable} from "inversify";
import {di, log} from "@dp-instant/core";
import {ISocial, TypesSocial} from "@dp-instant/social";
import {EventEmitter} from "eventemitter3";

@injectable()
/**
 * @deprecated
 */
export class LeaderboardGlobal extends EventEmitter {
    public static readonly UPDATED: string = "LeaderboardGlobal.UPDATED";
    public static readonly PREFIX: string = "GlobalScores";

    // @inject(TypesSocial.model)
    private _socialModel: ISocial;
    private lb: Leaderboard;

    async sync(limit = 100): Promise<boolean> {
        if (!this.lb) {
            try {
                this.lb = await FBInstant.getLeaderboardAsync(LeaderboardGlobal.PREFIX);
            } catch (e) {
                log.error(e && e.message, e);
                return false;
            }
        }
        let entries: LeaderboardEntry[];
        try {
            entries = await this.lb.getConnectedPlayerEntriesAsync(limit, 0);
        } catch (e) {
            log.error(e && e.message, e);
            return false;
        }
        if (entries) {
            entries.forEach(entry => {
                const p = entry.getPlayer();
                const body = this.socialModel.getFriendById(p.getID() as string);
                if (body) {
                    body.scores.update(undefined, entry);
                    this.emit(LeaderboardGlobal.UPDATED);
                } else {
                    log.warn(p.getID(), p.getName(), "scores sync failed");
                }
            });
        } else {
            return false;
        }
        log.debug(entries);
        return true;
    }

    async submit(score: number, extraData?: any): Promise<void> {
        if (this.lb && score > this.socialModel.me.scoreGlobal) {
            let entry;
            try {
                entry = await this.lb.setScoreAsync(
                    score,
                    extraData ? JSON.stringify(extraData) : undefined
                );
            } catch (e) {
                log.warn(e);
                return;
            }
            this.socialModel.me.scores.update(undefined, entry);
        }
    }

    private get socialModel() {
        return this._socialModel || (this._socialModel = di.get(TypesSocial.model));
    }
}
