import {GlobalEventProvider} from "@dp-instant/core";
import {AdResponse} from "./AdResponse";
import {IAdManager} from "./IAdManager";
import {AdEvent} from "./AdEvent";
import {AdEvents} from "./AdEvents";

export class AdManagerBase extends GlobalEventProvider implements IAdManager {
    static config = {
        ids: {
            INTER: "",
            BANNER: "",
            REWARD: ""
        },
        throttling: {
            interstitial: {
                systemStart: Date.now(),
                showFirstDelay: 20 * 1000,
                showNextDelay: 60 * 1000,
                lastShow: 0
            }
        },
        interstitialsDisabled: false
    };
    protected static needToThrottleAd(conf: any): boolean {
        const systemStart = conf.systemStart;
        const lastShow = conf.lastShow;
        const firstShow = systemStart + conf.showFirstDelay;
        const timeNow = Date.now();
        if (timeNow < firstShow) {
            return true;
        }

        if (lastShow) {
            const nextShowTime = lastShow + conf.showNextDelay;
            if (timeNow < nextShowTime) {
                return true;
            }
        }
        return false;
    }
    async showAd(id: string, force = false, preloadNext = true): Promise<AdResponse> {
        return AdResponse.NOT_SUPPORTED;
    }
    async showRewardAd(id: string, preloadNext = true): Promise<AdResponse> {
        return AdResponse.NOT_SUPPORTED;
    }
    async preloadNextInter(placementID: string): Promise<void> {}
    async preloadNextReward(placementID: string): Promise<void> {}

    protected onAdStarted(paload: AdEvent) {
        this.dispatch(AdEvents.STARTED, paload);
    }
    protected onAdEnded(paload: AdEvent) {
        this.dispatch(AdEvents.ENDED, paload);
    }
}
