import {Action} from "@dp-instant/core";
import {TypesFlow} from "@dp-instant/gameflow";
import {ISocial, ScoreType, TypesSocial} from "@dp-instant/social";
import {TypesUI, UIEvents} from "@dp-instant/ui";
import {inject, injectable} from "inversify";

@injectable()
export class EndScreenAction extends Action<boolean> {
    @inject(TypesSocial.model)
    protected social: ISocial;

    @inject(TypesFlow.LevelStart)
    protected levelStart: Action<void>;

    public async execute(isWon = false) {
        if (this.social.inGroup) {
            this.dispatch(UIEvents.SCREEN_CHANGED, {
                id: TypesUI.screen.LEADERBOARD,
                props: {
                    users: this.social.contextPlayers,
                    scoreType: ScoreType.CONTEXT,
                    overlay: false,
                    onClose: () => this.levelStart.run()
                }
            });
        } else {
            if (isWon) {
                this.dispatch(UIEvents.SCREEN_CHANGED, {
                    id: TypesUI.screen.WIN,
                    props: {
                        opponent: this.social.opponent,
                        nextMatch: this.social.getRandomOpponent()
                    }
                });
            } else {
                this.dispatch(UIEvents.SCREEN_CHANGED, {
                    id: TypesUI.screen.GAME_OVER,
                    props: {
                        opponent: this.social.opponent,
                        nextMatch: this.social.inSolo
                            ? this.social.getRandomOpponent()
                            : undefined /* replay */
                    }
                });
            }
        }
        this.resolve();
    }
}
