import "./ShareComponent.less";
import {Icon} from "@dp-instant/ui";
import {Component, ComponentChild, h} from "preact";
import {Action, lazyGet, Localize} from "@dp-instant/core";
import {TypesFlow} from "@dp-instant/gameflow";
import cn from "classnames";

export type ShareComponentProps = {
    visible?: boolean;
    screenshot: string;
};
export class ShareComponent extends Component<ShareComponentProps, any> {
    render(props: ShareComponentProps): ComponentChild {
        return (
            <div
                class={cn("share-cont", {visible: props.visible})}
                onClick={() => lazyGet<Action>(TypesFlow.share)?.run()}
            >
                <div className={"share_ico"}>
                    <Icon type={"share"} />
                    <div>{Localize.get("share", "Share")}</div>
                </div>
                <img className={"screenshot"} src={props.screenshot} alt={""} />
            </div>
        );
    }
}
