import Leaderboard = FBInstant.Leaderboard;
import LeaderboardEntry = FBInstant.LeaderboardEntry;
import {log} from "@dp-instant/core";
import {injectable} from "inversify";
import {ILeaderboard, ISocial, TypesSocial} from "@dp-instant/social";
import {di} from "@dp-instant/core";
import {EventEmitter} from "eventemitter3";
import {CommonEvents} from "@dp-instant/core";

@injectable()
export class LeaderboardContext extends EventEmitter implements ILeaderboard {
    public static readonly PREFIX: string = "Scores";

    // @inject(TypesSocial.model)
    private _socialModel: ISocial;
    private lb?: Leaderboard;
    private _lastContextId?: string | null;

    async sync(): Promise<boolean> {
        delete this.lb;
        if (this.socialModel.inSolo) {
            delete this._lastContextId;
            return false;
        }
        // if (this._lastContextId === FBInstant.context.getID()) {
        //     return Promise.resolve(false);
        // }
        this._lastContextId = FBInstant.context.getID();

        try {
            this.lb = await FBInstant.getLeaderboardAsync(
                LeaderboardContext.PREFIX + "." + FBInstant.context.getID()
            );
        } catch (e) {
            log.error(e && e.message, e);
            return false;
        }

        const getFriendsEntries = async () => {
            let _entries: LeaderboardEntry[] = [];
            try {
                _entries = await this.lb!.getEntriesAsync(10, 0);
            } catch (e) {
                log.warn("getEntriesAsync", e && e.message, e);
            }
            for (const entry of _entries) {
                const fbPlayer = entry.getPlayer();
                if (fbPlayer) {
                    const body = this.socialModel.getFriendById(
                        fbPlayer.getID() + "",
                        this.socialModel.contextPlayers.concat(this.socialModel.friends)
                    );
                    if (body) {
                        body.scores.update(entry);
                    } else {
                        log.warn(
                            fbPlayer.getID(),
                            fbPlayer.getName(),
                            "in leaderboard but neither in context nor friends"
                        );
                    }
                }
            }
        };
        const getPlayerEntry = async () => {
            try {
                const playerEntry = await this.lb!.getPlayerEntryAsync();
                if (playerEntry) {
                    this.socialModel.me.scores.update(playerEntry);
                }
            } catch (e) {
                log.warn("getPlayerEntryAsync", e && e.message, e);
            }
        };

        await Promise.all([getPlayerEntry(), getFriendsEntries()]);
        this.emit(CommonEvents.UPDATED);

        return true;
    }

    async submit(score: number, extraData?: any): Promise<boolean> {
        const dataToPost = extraData ? JSON.stringify(extraData) : undefined;
        let entry;
        try {
            entry = await this.lb?.setScoreAsync(score, dataToPost);
        } catch (e) {
            log.warn(e);
            return false;
        }
        log.trace("submit", score, dataToPost);
        this.socialModel.me.scores.update(entry, undefined);
        return true;
    }

    private get socialModel() {
        return this._socialModel || (this._socialModel = di.get(TypesSocial.model));
    }
}
