import {Action, IGameConfigJSON, log, TypesCore} from "@dp-instant/core";
import {injectable, inject} from "inversify";
import {TypesApp} from "../TypesApp";
import {AppModel} from "../model/AppModel";

@injectable()
export class AuthFacebookAction extends Action {
    @inject(TypesApp.model)
    protected model: AppModel;
    @inject(TypesCore.gameConfig)
    protected gameConfig: IGameConfigJSON;

    async execute() {
        const playerInfo = await FBInstant.player.getSignedPlayerInfoAsync();
        const id = playerInfo.getPlayerID();
        const signature = playerInfo.getSignature();
        const {id: gameId} = this.gameConfig;

        const response = await this.model.post({id, signature}, `${gameId}/auth`);
        if (response && response.token) {
            await this.model.auth(response.token);
        } else {
            log.warn("Auth failed, response is", response);
        }

        this.resolve();
    }
}
