import {injectable} from "inversify";
import {IAdManager, SocialEvents, TypesAds, TypesNotification} from "@dp-instant/social";
import {TypesUI} from "@dp-instant/ui";
import {Action, di, lazyGet, TypesCore} from "@dp-instant/core";
import {SocialFlowAction} from "./SocialFlowAction";

@injectable()
export class LevelStartAction<T = never> extends SocialFlowAction<T> {
    protected TRACK_EVENT = "level_start";
    public waitForContextChange = false;

    protected async launch(data?: T): Promise<any> {
        this.social.me.scoreSession = 0;
        const {facebook} = di.get(TypesCore.gameConfig);
        if (facebook?.useBannerAd) {
            const manager = di.get<IAdManager>(TypesAds.manager);
            manager.showBanner && manager.showBanner();
        }
        await lazyGet<Action>(TypesUI.startScreenAction)?.run();
    }

    protected async beforeLaunch(): Promise<void> {
        await Promise.all([
            (async () => {
                if (this.waitForContextChange) {
                    await new Promise(resolve =>
                        this.social.once(SocialEvents.CONTEXT_CHANGE, resolve)
                    );
                }
                this.sendPush(TypesNotification.start); // only after context's switched
            })(),
            super.beforeLaunch()
        ]);
    }
}
