import "./SocialOverlay.less";
import {h} from "preact";
import {ISocial, SocialEvents, TypesSocial} from "@dp-instant/social";
import {lazyGet} from "@dp-instant/core";
import {useEffect, useState} from "preact/hooks";
import cn from "classnames";

export const SocialOverlay = () => {
    const [active, setState] = useState(false);
    const social: ISocial | undefined = lazyGet(TypesSocial.model);
    if (social) {
        const onShowOverlay = () => setState(true);
        const onHideOverlay = () => setState(false);
        useEffect(() => {
            social.on(SocialEvents.SHOW_OVERLAY, onShowOverlay);
            social.on(SocialEvents.HIDE_OVERLAY, onHideOverlay);
            return () => {
                social.off(SocialEvents.SHOW_OVERLAY, onShowOverlay);
                social.off(SocialEvents.HIDE_OVERLAY, onHideOverlay);
            };
        }, [social]);
    }
    return <div className={cn("social__overlay", {active})} />;
};
