import {h, FunctionComponent, JSX} from "preact";
import cn from "classnames";
import {Score} from "../Score/Score";
import {Button, ButtonProps} from "../Button/Button";
import {ComponentClassName, Icon} from "@dp-instant/ui";
import {IconType} from "../Icon/icons/iconType";

interface AvatarPlayWithBtnProps extends ButtonProps {
    content?: string;
    visible?: boolean;
}

export interface AvatarProps extends ComponentClassName {
    imgPath?: string;
    imgAlt?: string;
    score?: number | string;
    scoreIconType?: IconType;
    playWithBtn?: AvatarPlayWithBtnProps;
}

const AvatarPlayWithBtn = ({
    content,
    icon,
    className,
    visible,
    onClick
}: AvatarPlayWithBtnProps) => {
    const classNames = cn("avatar-play-with-btn", className, {
        "avatar-play-with-btn-visible": visible
    });
    return (
        <div className={classNames}>
            <Button icon={icon} type="primary" shape="oval" bordered onClick={onClick}>
                {content}
            </Button>
        </div>
    );
};

export const Avatar: FunctionComponent<AvatarProps> = ({
    className,
    imgPath,
    score,
    scoreIconType,
    imgAlt = "Avatar",
    playWithBtn
}) => {
    const classNames = cn("avatar", {"avatar-play-with": playWithBtn}, className);
    return (
        <span className={classNames}>
            <span className="avatar-inner">
                {imgPath ? (
                    <img src={imgPath} alt={imgAlt} />
                ) : (
                    <Icon type="placeholder-avatar" className="avatar-bg" />
                )}
            </span>
            {playWithBtn && <AvatarPlayWithBtn {...playWithBtn} />}
            {(score || score === 0) && (
                <Score className="avatar-score" icon={scoreIconType}>
                    {score}
                </Score>
            )}
        </span>
    );
};
