import "./PauseOverlay.less";
import {h} from "preact";
import {Action, CommonEvents, lazyGet} from "@dp-instant/core";
import {TypesSocial} from "@dp-instant/social";
import cn from "classnames";
import {useState} from "preact/hooks";
import {eventListenerHook} from "../../hooks/eventListenerHook";

export const PauseOverlay = () => {
    const [paused, setState] = useState(false);
    eventListenerHook(CommonEvents.PAUSE, (paused: boolean) => setState(paused), []);
    const doPause = () => lazyGet<Action<boolean>>(TypesSocial.pauseAction)?.run(false);
    return (
        <div className={cn("pause__overlay", {hidden: !paused})} onClick={doPause}>
            <div className={"title"}>Tap to continue</div>
        </div>
    );
};
