import {injectable} from "inversify";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/remote-config";
import App = firebase.app.App;
import RemoteConfig = firebase.remoteConfig.RemoteConfig;
import {BackendModel} from "./BackendModel";
import {log} from "@dp-instant/core";
import {SocialPlatformProvider} from "../../index";

@injectable()
export class AppModel extends BackendModel {
    protected _firebaseApp: App;
    protected _remoteConfig: RemoteConfig;

    async init(config: {firebase: any; provider: SocialPlatformProvider; host?: string}) {
        if (process.env.NODE_ENV === "development") {
            (window as any).app = this;
        }
        const {firebase: firebaseConfig} = config;
        this._firebaseApp = firebase.initializeApp(firebaseConfig);
        this._remoteConfig = this._firebaseApp.remoteConfig();
        this._remoteConfig.defaultConfig = this.getDefaultRemoteConfig(config.host);
        // TODO: add
        // Localize.addTemplates(getJSONFromRemoteConfig(remoteConfig, "i18n", {}));

        try {
            await this._remoteConfig.ensureInitialized().catch(log.warn);
            await this._remoteConfig.fetchAndActivate().catch(log.warn);
        } catch (e) {
            log.warn("Failed to activate remote config", e);
        }

        this._defaultHost = `${this._remoteConfig.getString("backend")}/${config.provider}`;
        log.info("defaultHost", this._defaultHost);
    }

    public async auth(token: string) {
        let credeintials;
        try {
            credeintials = await this._firebaseApp.auth().signInWithCustomToken(token);
        } catch (e) {
            //
        }
        if (credeintials) {
            log.info("User authorized", credeintials);
        }
    }
    public get firebaseApp(): App {
        return this._firebaseApp;
    }
    public get defaultEndpoint(): string {
        return "";
    }
    public get remoteConfig(): RemoteConfig {
        return this._remoteConfig;
    }
    protected getDefaultRemoteConfig(backend = "http://localhost") {
        return {
            backend,
            payments_backend_url: `${backend}/purchase`
        };
    }
}
