import {lazyGet} from "@dp-instant/core";
import {UIEvents} from "@dp-instant/ui";
import {h} from "preact";
import {useState} from "preact/hooks";
import {useEventListenerEffect} from "../hooks/useEventListenerEffect";
type ScreensState = {
    CurrentScreen: any;
    ScreenProps: any;
};

export const Screens = () => {
    const [state, setState] = useState<ScreensState>(() => ({
        CurrentScreen: undefined,
        ScreenProps: undefined
    }));
    useEventListenerEffect(
        UIEvents.SCREEN_CHANGED,
        (event: any) => {
            const {id, props: CurrentScreenProps} = event;
            setState({
                ...state,
                CurrentScreen: lazyGet(id),
                ScreenProps: CurrentScreenProps
            });
        },
        [state]
    );
    const {CurrentScreen} = state;
    return CurrentScreen && h(CurrentScreen, state.ScreenProps);
};
