import {h, FunctionComponent, JSX} from "preact";
import cn from "classnames";
import {Icon} from "../Icon/Icon";
import {IconType} from "../Icon/icons/iconType";
import {ComponentClassName} from "../../interfaces";

export interface ScoreProps extends ComponentClassName {
    icon?: JSX.Element | IconType;
}

const getIcon = (icon: JSX.Element | IconType) => {
    if (typeof icon === "string") {
        return <Icon type={icon} width={35} height={35} />;
    }
    return icon;
};

export const Score: FunctionComponent<ScoreProps> = ({className, icon, children, ...props}) => {
    const classNames = cn("score", className);
    const IconComponent = icon && getIcon(icon);
    return (
        <span className={classNames} {...props}>
            {IconComponent}
            <span className="score-content">{children}</span>
        </span>
    );
};
