import {Action, di, IGameConfigJSON, log, TypesCore} from "@dp-instant/core";
import {AppModel} from "../model/AppModel";
import {TypesApp} from "../TypesApp";
import {inject, injectable} from "inversify";
import {TypesSocial} from "../../TypesSocial";
import {ISocial} from "../../interfaces";
import {FirebaseAnalyticsProvider, IATracker, TypesAnalytics} from "@dp-instant/analytics";

@injectable()
export class LoginAction extends Action {
    @inject(TypesApp.model)
    private model: AppModel;

    @inject(TypesSocial.model)
    private social: ISocial;

    @inject(TypesCore.gameConfig)
    protected gameConfig: IGameConfigJSON;

    @inject(TypesAnalytics.tracker)
    protected analytics: IATracker;

    async execute() {
        const {analytics} = this.gameConfig;
        if (analytics && analytics.firebase) {
            await this.model.init({
                firebase: analytics.firebase,
                host: this.gameConfig.backend?.host,
                provider: this.social.socialPlatform
            });
            // TODO: check isSupported
            const firebaseTracker = new FirebaseAnalyticsProvider();
            firebaseTracker.init(this.model.firebaseApp);
            this.analytics.addProvider(firebaseTracker);
        } else {
            log.warn("Firebase analytics is not configured");
            return this.resolve();
        }
        await this.loginUser();
        this.resolve();
    }
    async loginUser(): Promise<boolean> {
        if (di.isBound(TypesApp.authAction)) {
            await di.get<Action>(TypesApp.authAction).run();
        } else {
            log.warn("Skip user login");
        }
        return false;
    }
}
