import {h, FunctionComponent} from "preact";
import cn from "classnames";

export interface CountProps {
    className?: string;
    count?: string | number;
}

export const Count: FunctionComponent<CountProps> = ({className, count = 0, ...props}) => {
    const classNames = cn("count", className);
    return (
        <span className={classNames} {...props}>
            {count}
        </span>
    );
};
