import {AnalyticsModule} from "@dp-instant/analytics";
import {AudioModule} from "@dp-instant/audio";
import {CoreModule, di} from "@dp-instant/core";
import {CrossPromoModule} from "@dp-instant/crosspromo";
import {GameFlowModule} from "@dp-instant/gameflow";
import {SetupSentryAction} from "./SetupSentryAction";

import {
    AdsModuleFacebook,
    AppModule,
    AuthFacebookAction,
    NotificationsModule,
    SocialModuleFacebook,
    TypesApp
} from "@dp-instant/social";
import {ContainerModule} from "inversify";

export const GameModuleFacebook = new ContainerModule(() => {
    di.load(
        CoreModule,
        AppModule,
        AudioModule,
        AnalyticsModule,
        AdsModuleFacebook,
        SocialModuleFacebook,
        NotificationsModule,
        GameFlowModule,
        CrossPromoModule
    );
    di.bind(TypesApp.authAction).to(AuthFacebookAction);
    di.bind("sentryAction").to(SetupSentryAction);
});
