import {Action} from "@dp-instant/core";
import {FBPOPUP, ISocial, IUser, TypesSocial} from "@dp-instant/social";
import {inject} from "inversify";
import {TypesFlow} from "../TypesFlow";

export class PlayWithOpponentAction extends Action<IUser | undefined> {
    @inject(TypesSocial.model)
    protected social: ISocial;

    @inject(TypesFlow.LevelStart)
    protected levelStart: Action;

    async execute(idOrOpponent?: IUser | undefined): Promise<any> {
        const status = await this.social.playWith(idOrOpponent, true);
        if (status === FBPOPUP.ACCEPTED) {
            await this.levelStart.run();
            this.resolve();
        }
        this.reject();
    }
}
