import {
    LeaderboardContext,
    LeaderboardGlobalExternal,
    TypesSocial,
    YaPlayer
} from "@dp-instant/social";
import {ContainerModule, interfaces} from "inversify";
import {PaymentsModelYandex} from "./model/PaymentsModelYandex";
import {CookieDataYandex} from "./data/CookieDataYandex";
import {SocialModelYandex} from "./model/SocialModelYandex";
import {UserScore} from "./model/user/UserScore";
import {UserYandex} from "./model/user/UserYandex";

export const SocialModuleYandex = new ContainerModule((
    bind: interfaces.Bind /*, unbind, isBound, rebind*/
) => {
    bind(TypesSocial.model).to(SocialModelYandex).inSingletonScope();
    bind(TypesSocial.cookie).to(CookieDataYandex);
    bind(TypesSocial.payments).to(PaymentsModelYandex);
    bind(TypesSocial.user).to(UserYandex);
    bind(TypesSocial.userScore).to(UserScore);
    bind(TypesSocial.leaderboardGlobal).to(LeaderboardGlobalExternal).inSingletonScope();
    bind(TypesSocial.leaderboardContext).to(LeaderboardContext).inSingletonScope();

    let dummyUser: UserYandex;
    bind(TypesSocial.dummyUser).toDynamicValue((context: interfaces.Context) => {
        if (!dummyUser) {
            dummyUser = context.container.get<UserYandex>(TypesSocial.user);
            dummyUser.init({
                getID: () => "unknown",
                getPhoto: () =>
                    URL.createObjectURL(
                        new Blob(
                            [
                                `
                    <svg xmlns="http://www.w3.org/2000/svg" width="320" height="320">
                        <path d="M0 0h320v320H0V0z" fill="#fff"/>
                        <path d="M320 0H0v320h320V0zM160 66.7c22.1 0 40 17.9 40 40s-17.9 40-40 40-40-17.9-40-40 17.9-40 40-40zm0 189.3c-33.4 0-62.7-17.1-80-42.9.3-26.5 53.4-41.1 80-41.1s79.6 14.6 80 41.1c-17.3 25.8-46.6 42.9-80 42.9z"/>
                    </svg>`
                            ],
                            {type: "image/svg+xml"}
                        )
                    )
            } as YaPlayer);
        }
        return dummyUser;
    });
});
