import {injectable} from "inversify";
import {
    IAdManager,
    ILeaderboard,
    IUser,
    TypesAds,
    TypesNotification,
    TypesSocial
} from "@dp-instant/social";
import {TypesFlow} from "@dp-instant/gameflow";
import {Action, di, lazyGet, TypesCore} from "@dp-instant/core";
import {TypesUI} from "@dp-instant/ui";
import {SocialFlowAction} from "./SocialFlowAction";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";

@injectable()
export class LevelEndAction<T = boolean | undefined> extends SocialFlowAction<T> {
    protected TRACK_EVENT = "level_end";
    private _won: boolean;

    protected async beforeLaunch(): Promise<void> {
        this._won = this.isWon();

        const {facebook} = di.get(TypesCore.gameConfig);
        if (facebook?.useBannerAd) {
            di.get<IAdManager & {showBanner: () => void}>(TypesAds.manager).showBanner();
        }
        await Promise.all([this.submitScore(), this.showAdIfNeeded()]);
    }

    protected async launch(): Promise<void> {
        this.sendPush(TypesNotification.finish, {isWon: this._won});
        await lazyGet<Action<boolean>>(TypesUI.endScreenAction)?.run(this._won);
    }

    protected async submitScore(): Promise<void> {
        this.social.postSessionScore();
        this.leaderboardGlobal?.submit(this.social.me.scoreSession).then();
        if (this.social.me.scoreSession > this.social.me.scoreGlobal) {
            lazyGet<Action>(TypesFlow.share)?.run();
        }

        if (!this.social.inSolo) {
            await this.leaderboardContext?.submit(this.social.me.scoreSession);
        }
    }

    protected needToShowAD(): boolean {
        return false;
    }

    protected isWon(): boolean {
        if (typeof this.data === "boolean") {
            return this.data;
        }
        if (this.social.inSolo) {
            return this.social.me.scoreSession > this.social.me.scoreGlobal;
        }
        if (this.social.inBlindContext) {
            return true;
        }
        if (this.social.inGroup) {
            const [winner] = this.social.contextPlayers.sort(
                (a: IUser, b: IUser) => b.scoreContext - a.scoreContext
            );
            if (winner) {
                return (
                    Math.max(this.social.me.scoreContext, this.social.me.scoreSession) >=
                    winner.scoreContext
                );
            }
        }
        return !!(
            this.social.opponent && this.social.me.scoreSession > this.social.opponent.scoreContext
        );
    }
    protected get leaderboardContext(): ILeaderboard | undefined {
        return lazyGet(TypesSocial.leaderboardContext);
    }
    protected get leaderboardGlobal(): ILeaderboard | undefined {
        return lazyGet(TypesSocial.leaderboardGlobal);
    }
    protected track() {
        lazyGet<IATracker>(TypesAnalytics.tracker)?.track(this.TRACK_EVENT, 1, {
            status: this.isWon() ? "won" : "loose"
        });
    }
}
