import "./AlertsOverlay.less";
import {h} from "preact";
import {AdEvent, AdEvents, AdResponse} from "@dp-instant/social";
import {IEventEmitter, lazyInject, Localize, TypesCore} from "@dp-instant/core";
import cn from "classnames";
import StatusAlert, {StatusAlertService} from "./status-alert";
import "./status-alert/status-alert.css";
import {GlobalEventProviderComponent} from "../GlobalEventProviderComponent";

type ActiveState = {
    active: boolean;
    alertId?: string;
};
export class Alerts extends GlobalEventProviderComponent<any, ActiveState> {
    @lazyInject(TypesCore.dispatcher)
    protected dispatcher: IEventEmitter;

    public componentWillUnmount() {
        this.addListener(AdEvents.STARTED, this.onAdStarted);
        this.addListener(AdEvents.ENDED, this.onAdEnded);
    }
    protected onAdStarted(e: AdEvent) {
        StatusAlertService.removeAlert(this.state.alertId!);
        this.setState({active: true});
    }

    protected onAdEnded(payload: AdEvent) {
        const {status} = payload;
        let alertId = undefined;
        if (payload.reward) {
            switch (status) {
                case AdResponse.UNKNOWN:
                case AdResponse.FAILED:
                    alertId = StatusAlertService.showError(Localize.get("video_ad_failed"));
                    break;
                case AdResponse.NO_FILL:
                    alertId = StatusAlertService.showWarning(Localize.get("no_video_ad"));
                    break;
                case AdResponse.CANCELLED:
                    alertId = StatusAlertService.showWarning(Localize.get("video_ad_cancelled"));
                    break;
            }
        }

        this.setState({active: false, alertId});
    }

    public render(props: any, state: ActiveState) {
        return (
            <div className={cn("ads__overlay")}>
                <StatusAlert />
                <div
                    className={cn("bg", {
                        active: state.active
                    })}
                />
            </div>
        );
    }
}
