import {injectable} from "inversify";
import {log} from "@dp-instant/core";
import {ICookieData} from "./ICookieData";

type DataObject = any;
type Dict = {
    [key: string]: any;
};

@injectable()
export class CookieDataFacebook implements ICookieData {
    static DEFAULT_UPLOAD_DELAY = 1000;
    protected lastSync: number;
    protected cache: Map<string, DataObject> = new Map<string, DataObject>();
    protected _uploadTimeout: any;

    async get(k?: string | string[]): Promise<Dict> {
        const keys: string[] = (Array.isArray(k) ? k : [k as string]) || [];

        const newKeys = keys.filter(key => !this.cache.has(key));
        newKeys.forEach(key => this.cache.set(key, undefined));
        await this.downloadFromRemote(newKeys);
        const obj: {[key: string]: {}} = {};
        keys.forEach(_ => (obj[_] = this.cache.get(_)));
        return obj;
    }
    save(key: string, data: DataObject, delay = CookieDataFacebook.DEFAULT_UPLOAD_DELAY) {
        this.cache.set(key, data);
        clearTimeout(this._uploadTimeout);
        this._uploadTimeout = setTimeout(() => this.uploadToRemote(), delay);
        return data;
    }
    async erase(key: string): Promise<boolean> {
        this.cache.set(key, undefined);
        clearTimeout(this._uploadTimeout);
        this._uploadTimeout = setTimeout(
            () => this.uploadToRemote(),
            CookieDataFacebook.DEFAULT_UPLOAD_DELAY
        );
        return true;
    }
    async uploadToRemote(keys?: string[]): Promise<void> {
        const obj: {[key: string]: {}} = {};
        this.cache.forEach((v, k) => {
            if (!keys || keys.includes(k)) {
                obj[k] = v;
            }
        });
        log.debug(this, "setDataAsync", obj);
        return FBInstant.player.setDataAsync(obj);
    }

    protected async downloadFromRemote(keys?: string[]) {
        const allKeys = Array.from(this.cache.keys());
        const rawData = await FBInstant.player.getDataAsync(
            isNaN(this.lastSync) ? allKeys : keys || allKeys
        );
        log.debug(this, "getDataAsync", rawData);
        if (rawData) {
            Object.keys(rawData).forEach(i => {
                this.cache.set(i, rawData[i]);
            });
        }
        this.lastSync = Date.now();
    }
}
