import {h, FunctionComponent, JSX} from "preact";
import cn from "classnames";
import {Score, ScoreProps} from "../Score/Score";
import {Icon, IconProps} from "../Icon/Icon";

export interface ScoreGroupProps {
    className?: string;
}

const ScoreGroupIcon: FunctionComponent<IconProps> = props => <Icon {...props} />;

const ScoreGroupScore: FunctionComponent<ScoreProps> = props => <Score {...props} />;

export const ScoreGroup: FunctionComponent<ScoreGroupProps> & {
    Icon: typeof ScoreGroupIcon;
    Score: typeof ScoreGroupScore;
} = ({className, ...props}) => {
    const classNames = cn("score-group", className);
    return <div className={classNames} {...props} />;
};

ScoreGroup.Icon = Icon;
ScoreGroup.Score = Score;
