import {User} from "./user/User";
import {inject, injectable} from "inversify";
import {
    CookieDataFacebook,
    FBPOPUP,
    ILeaderboard,
    ISocial,
    ISocialInitParams,
    IUser,
    ScoreType,
    SocialEvents,
    SocialPlatformProvider,
    TypesSocial
} from "@dp-instant/social";
import {
    Action,
    di,
    lazyGet,
    Locale,
    Localize,
    log,
    Random,
    TypesCore,
    WaitAction
} from "@dp-instant/core";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";
import {EventEmitter} from "eventemitter3";
import Player = FBInstant.Player;
import LeaderboardUpdatePayload = FBInstant.LeaderboardUpdatePayload;
import CustomUpdatePayload = FBInstant.CustomUpdatePayload;
import ContextOptions = FBInstant.ContextOptions;
import {SessionData} from "../data/SessionData";
import {PaymentsModelFacebook} from "./PaymentsModelFacebook";

const AKeys = {
    PLAY_SOLO: "play_solo",
    PLAY_OFFLINE: "play_offline",
    PLAY_OFFLINE_FAILED: "play_offline_failed",
    PLAY_WITH: "play_with",
    PLAY_WITH_CLOSE: "play_with_close",
    CHOOSE_ASYNC: "play_choose_async",
    CHOOSE_ASYNC_CLOSE: "play_choose_async_close",
    UPDATE_ASYNC: "push",
    SHARE: "share",
    SWITCH_GAME: "switch_game",
    SHORTCUT: "shortcut",
    SHORTCUT_BOT: "bot_shortcut"
};
const track = (name: string, v = 1, p?: any) =>
    lazyGet<IATracker>(TypesAnalytics.tracker)?.track("social_" + name, v, p);
const popupStatusFromError = (error?: {code: string}) => {
    switch (error?.code) {
        case "USER_INPUT":
            return FBPOPUP.CANCELLED;
        case "SAME_CONTEXT":
            return FBPOPUP.ACCEPTED;
        case "PENDING_REQUEST":
            return FBPOPUP.REJECTED;
        case "INVALID_PARAM":
        case "NETWORK_FAILURE":
        case "CLIENT_UNSUPPORTED_OPERATION":
        default:
            return FBPOPUP.FAILED;
    }
};

@injectable()
export class SocialModelFacebook extends EventEmitter implements ISocial {
    /**
     * @deprecated
     * @private
     */
    @inject(TypesSocial.leaderboardGlobal)
    private _leaderboardGlobal: ILeaderboard;
    /**
     * @deprecated
     * @private
     */
    @inject(TypesSocial.leaderboardContext)
    private _leaderboardContext: ILeaderboard;

    private static CONTEXT_CACHE: {[key: string]: string} = {};
    public readonly leaderboards: ILeaderboard[] = [];

    @inject(TypesSocial.cookie)
    public readonly cookie: CookieDataFacebook;
    public readonly session: SessionData = new SessionData();

    @inject(TypesSocial.payments)
    public readonly payments: PaymentsModelFacebook;

    private _forcedSolo = false;
    private _matchedContext = false;
    private _invitedContext = false;
    private _opponent: User | undefined;

    private _me: User;
    private _nobody: User;
    private _contextPlayers: User[] = [];
    private _friends: User[] = [];

    private _initialPromise?: Promise<any>;

    constructor() {
        super();
        if (process.env.NODE_ENV === "development") {
            Object.defineProperty(this, "d", {
                get: () => {
                    const prnt = (u: User) =>
                        `${u.name} (${u.scores.getScore(ScoreType.CONTEXT) || "-"}/${
                            u.scores.getScore(ScoreType.GLOBAL) || "-"
                        })  #${u.id} ${u.isNew ? "(new)" : ""}`;
                    const ctxPlayers = this.contextPlayers;
                    console.group("Name (context/global) #id");
                    log.info("* " + prnt(this.me));
                    console.groupEnd();
                    console.group("Friends");
                    this._friends.forEach(f =>
                        log.debug((ctxPlayers.indexOf(f) !== -1 ? "* " : " ") + prnt(f))
                    );
                    console.groupEnd();
                }
            });
        }
    }

    protected createUser(source: Player, isNew = false): User {
        return di.get<User>(TypesSocial.user).init(source, isNew);
    }

    private emitUpdate() {
        log.trace(SocialEvents.CONTEXT_CHANGE, FBInstant.context.getID());
        this.emit(SocialEvents.CONTEXT_CHANGE);
    }

    async syncContextPlayers(): Promise<void> {
        this._contextPlayers = [];
        if (this.inSolo) {
            return Promise.resolve();
        }
        let ctxPlayers: FBInstant.ContextPlayer[] = [];
        try {
            ctxPlayers = await FBInstant.context.getPlayersAsync();
        } catch (error) {
            log.warn("getPlayersAsync", error);
            return;
        }
        const arr = ctxPlayers.slice(0);
        while (arr.length) {
            const ctxPlayer = arr.pop();
            const item = this.getFriendById(ctxPlayer?.getID());
            if (item) {
                this._contextPlayers.push(item);
            } else {
                // user is not in friends
                this._contextPlayers.push(this.createUser((ctxPlayer as unknown) as Player));
            }
        }
        if (this._contextPlayers.indexOf(this._me) === -1) {
            this._contextPlayers.push(this._me);
        }
        if (
            this._opponent &&
            this._opponent !== this.nobody &&
            this._contextPlayers.indexOf(this._opponent) === -1 &&
            !this._invitedContext
        ) {
            // inactive context case
            this._contextPlayers.push(this._opponent);
        }
    }

    public scoreNum(u?: User): number {
        if (this.inGroup) {
            return (
                u?.scores.getScore(ScoreType.SESSION | ScoreType.CONTEXT | ScoreType.GLOBAL) || 0
            );
        }
        return u?.scores.getScore(ScoreType.SESSION | ScoreType.CONTEXT) || 0;
    }

    private sortContextPlayers() {
        this._contextPlayers = this._contextPlayers.sort(
            (a, b) => this.scoreNum(b) - this.scoreNum(a)
        );
    }

    private async switchOrCreateContext(id: string): Promise<void> {
        const cache = SocialModelFacebook.CONTEXT_CACHE;
        const cachedID = cache[id];

        const createContext = async () => {
            await FBInstant.context.createAsync(id);
            cache[id] = FBInstant.context.getID() + "";
        };

        if (!cachedID) {
            return createContext();
        }

        try {
            await FBInstant.context.switchAsync(cachedID);
        } catch (error) {
            log.warn(error);
            const status = popupStatusFromError(error);
            switch (status) {
                case FBPOPUP.REJECTED:
                case FBPOPUP.FAILED:
                    delete cache[id];
                    return createContext();
            }
        }
    }

    private syncCurrentOpponent() {
        this._opponent = (() => {
            if (this.inSolo) {
                return undefined;
            }
            if (this.inGroup) {
                return this.nobody;
            }

            for (const cp of this._contextPlayers) {
                if (cp.id !== this._me.id) {
                    return cp;
                }
            }
        })();
    }

    getFriendById(id?: string, friends?: any[]): User | undefined {
        friends = friends || this._friends;
        if (id === this._me.id) {
            return this._me;
        }
        for (const f of friends) {
            if (f.id === id) {
                return f;
            }
        }
    }
    async startGameAsync(): Promise<void> {
        await FBInstant.startGameAsync();
        Localize.defaultLocale = FBInstant.getLocale() as Locale;
    }
    async init(params?: ISocialInitParams): Promise<void> {
        if (this._friends.length) {
            return Promise.resolve();
        }
        if (this._initialPromise) {
            return this._initialPromise;
        }
        this._me = this.createUser(FBInstant.player);
        this._me.on("score_increased", () => {
            if (this.inGroup) {
                this.sortContextPlayers();
            }
        });
        if (process.env.NODE_ENV !== "development") {
            FBInstant.onPause(() => lazyGet<Action>(TypesSocial.pauseAction)?.run());
        }
        this.leaderboards.push(this._leaderboardGlobal, this._leaderboardContext);
        return (this._initialPromise = (async () => {
            const paymentsPromise = this.payments.init(
                lazyGet<any>(TypesCore.gameConfig)?.payments
            );
            const initialCookiePromise = this.cookie.get(["session"].concat(params?.cookies || []));
            let players: FBInstant.ConnectedPlayer[] = [];
            try {
                players = await FBInstant.player.getConnectedPlayersAsync();
            } catch (error) {
                delete this._initialPromise;
                log.warn(error);
                this._friends = [];
                return error;
            }
            const cookieData = this.session.init((await initialCookiePromise).session);
            const prevFriends = cookieData.friends;
            this._friends = players.map(u =>
                this.createUser(u as Player, !prevFriends.includes(u.getID()))
            );
            cookieData.friends = this.friends.map(f => f.id);
            log.trace("friends", this.friends);
            this.cookie.save("session", cookieData);

            FBInstant.setSessionData({
                gameSession: cookieData.gameSession,
                locale: FBInstant.getLocale()
            });

            await Promise.all([this.syncContextPlayers(), paymentsPromise]);
            this.sortContextPlayers();
            this.syncCurrentOpponent();
            log.debug("Social is READY");
            if (process.env.NODE_ENV === "development") {
                // @ts-ignore
                this.d;
            }
            this.emitUpdate();
            const {facebook} = lazyGet(TypesCore.gameConfig) || {facebook: {skipBotPopup: false}};
            if (!facebook.skipBotPopup) {
                await this.showBotPopup("startup");
            }
            if (!this.session.ftue) {
                await this.showShortcutPopup();
            }
        })());
    }

    async playSolo(): Promise<void> {
        track(AKeys.PLAY_SOLO);
        this._invitedContext = false;
        this._matchedContext = false;
        this._forcedSolo = true;
        this._opponent = undefined;
        await this.syncContextPlayers();
        this.emitUpdate();
    }
    async playOffline(tag = "default"): Promise<FBPOPUP> {
        if (this._matchedContext) {
            return FBPOPUP.REJECTED;
        }
        let canPlayerMatch = false;
        try {
            canPlayerMatch = await FBInstant.checkCanPlayerMatchAsync();
        } catch (e) {
            log.warn(e);
        }
        if (canPlayerMatch) {
            let status = FBPOPUP.ACCEPTED;
            track(AKeys.PLAY_OFFLINE);

            this.emit(SocialEvents.SHOW_OVERLAY);
            try {
                await FBInstant.matchPlayerAsync(tag, true, true);
            } catch (e) {
                status = popupStatusFromError(e);
                track(AKeys.PLAY_OFFLINE_FAILED, 1, e.code);
            }
            this.emit(SocialEvents.HIDE_OVERLAY);

            if (status == FBPOPUP.ACCEPTED) {
                this._matchedContext = true;
                this._invitedContext = false;
                this._forcedSolo = false;
                await this.syncContextPlayers();
                this.emitUpdate();
            } else {
                // FIXME: FBInstant bug freezes AD impression during matchmaking popup close
                await WaitAction.ms(500);
                status = FBPOPUP.CANCELLED;
            }
            track(AKeys.PLAY_OFFLINE, 1, {status});
            return status;
        }
        return FBPOPUP.REJECTED;
    }

    async playWith(idOrOpponent?: string | User, instantResolve = false): Promise<FBPOPUP> {
        track(AKeys.PLAY_WITH);
        const opponent =
            typeof idOrOpponent === "string" ? this.getFriendById(idOrOpponent) : idOrOpponent;

        if (!opponent || opponent === this._me || opponent === this._opponent) {
            if (opponent === this._me) {
                await this.playSolo();
            }
            return FBPOPUP.REJECTED;
        }

        this.emit(SocialEvents.SHOW_OVERLAY);
        const status: FBPOPUP = await (async () => {
            try {
                await this.switchOrCreateContext(opponent.id);
                this._me?.scores.flush();
                this._opponent?.scores.flush(); // context changed, old score is not actual anymore
                return FBPOPUP.ACCEPTED;
            } catch (error) {
                log.warn(error);
                return popupStatusFromError(error);
            }
        })();
        this.emit(SocialEvents.HIDE_OVERLAY);
        track(AKeys.PLAY_WITH_CLOSE, 1, {status});

        this._invitedContext = false;
        this._matchedContext = false;

        switch (status) {
            case FBPOPUP.FAILED:
            case FBPOPUP.CANCELLED:
                return status;
            default:
                this._forcedSolo = false;
                this._opponent = opponent;
        }
        const syncRoutine = async () => {
            await this.syncContextPlayers();
            await this.syncLeaderboards();
            this.emitUpdate();
        };
        if (instantResolve) {
            WaitAction.ms(1).then(syncRoutine);
        } else {
            await syncRoutine();
        }
        return status;
    }

    async notify(payload: CustomUpdatePayload | LeaderboardUpdatePayload): Promise<boolean> {
        let success = false;
        try {
            await FBInstant.updateAsync(payload);
            success = true;
        } catch (e) {
            log.warn("FBInstant.updateAsync rejected", e);
        }
        if (success) {
            track(AKeys.UPDATE_ASYNC, 1, (payload as CustomUpdatePayload).template);
        }
        return success;
    }
    async showShortcutPopup(): Promise<boolean> {
        let canCreate = false;
        try {
            canCreate = await FBInstant.canCreateShortcutAsync();
        } catch (e) {
            log.warn("canCreateShortcutAsync", e);
        }
        if (canCreate) {
            track(AKeys.SHORTCUT, 1, {status: "requested"});
            try {
                await FBInstant.createShortcutAsync();
            } catch (e) {
                log.warn("createShortcutAsync", e);
                return false;
            }
            track(AKeys.SHORTCUT, 1, {status: "created"});
            return true;
        }
        return false;
    }

    async showBotPopup(ref?: string): Promise<boolean> {
        if (FBInstant.getSupportedAPIs().indexOf("player.canSubscribeBotAsync") === -1) {
            return false;
        }
        try {
            await FBInstant.player.canSubscribeBotAsync();
        } catch (e) {
            log.warn("canSubscribeBotAsync", e);
            return false;
        }
        track(AKeys.SHORTCUT_BOT, 1, {status: "requested", ref});
        try {
            await FBInstant.player.subscribeBotAsync();
        } catch (e) {
            log.warn("subscribeBotAsync", e);
            return false;
        }
        track(AKeys.SHORTCUT_BOT, 1, {status: "created", ref});
        return true;
    }

    async share(payload: FBInstant.SharePayload): Promise<boolean> {
        this.emit(SocialEvents.SHOW_OVERLAY);
        let status = true;
        try {
            await FBInstant.shareAsync(payload);
        } catch (e) {
            log.warn("this.social.share failed", e);
            status = false;
        }
        track(AKeys.SHARE, 1, status ? "success" : "failed");
        this.emit(SocialEvents.HIDE_OVERLAY);
        return status;
    }

    async switchGame(id: string, data?: any): Promise<void> {
        track(AKeys.SWITCH_GAME, 1, id);

        try {
            await FBInstant.switchGameAsync(id, data);
        } catch (e) {
            log.warn("failed to switchGameAsync", e);
        }
    }

    async invite(options?: ContextOptions, instantResolve = false): Promise<FBPOPUP> {
        track(AKeys.CHOOSE_ASYNC);
        this.emit(SocialEvents.SHOW_OVERLAY);

        const status: FBPOPUP = await (async () => {
            try {
                await FBInstant.context.chooseAsync(options);
                return FBPOPUP.ACCEPTED;
            } catch (error) {
                log.warn(error);
                return popupStatusFromError(error);
            }
        })();

        this._forcedSolo = false;
        if (status === FBPOPUP.ACCEPTED) {
            // context changed, old score is not actual anymore
            this._me?.scores.flush();
            this._opponent?.scores.flush();

            this._opponent = undefined;
            this._invitedContext = true;
            this._matchedContext = false;

            const syncRoutine = async () => {
                await this.syncContextPlayers();
                await this.syncLeaderboards();
                await this.syncCurrentOpponent();
                this.emitUpdate();
            };
            if (instantResolve) {
                WaitAction.ms(0).then(syncRoutine);
            } else {
                await syncRoutine();
            }
        }
        this.emit(SocialEvents.HIDE_OVERLAY);
        track(AKeys.CHOOSE_ASYNC_CLOSE, 1, {status});

        return status;
    }

    getRandomChallenger(ignoreUsers: IUser[] = this.contextPlayers): IUser | undefined {
        return Random.from(...this.friends.filter(f => !ignoreUsers.includes(f)));
    }

    getRandomOpponent(): User | undefined {
        return Random.from(...this.friends.filter(f => this.contextPlayers.indexOf(f) === -1));
    }

    async syncLeaderboards(): Promise<void> {
        await Promise.all(this.leaderboards.map(lb => lb.sync()));
        this.sortContextPlayers();
    }

    getCurrentOpponent(): User | undefined {
        if (this._forcedSolo) {
            return undefined;
        }
        if (!this._opponent && FBInstant.context.getType() === "THREAD") {
            return this.nobody;
        }
        return this._opponent;
    }
    postSessionScore(score?: number): void {
        FBInstant.postSessionScore(isNaN(<number>score) ? this.me.scoreSession : score!);
    }

    get me(): User {
        return this._me;
    }

    get opponent(): User | undefined {
        if (this._forcedSolo) {
            return undefined;
        }
        if (!this._opponent && FBInstant.context.getType() === "THREAD") {
            return this.nobody;
        }
        return this._opponent;
    }
    get nobody(): User {
        return this._nobody || (this._nobody = di.get(TypesSocial.dummyUser));
    }
    get contextPlayers(): User[] {
        return this._contextPlayers;
    }

    get inBlindContext(): boolean {
        return (this._matchedContext || this._invitedContext) && this._contextPlayers.length < 2;
    }

    get inSolo(): boolean {
        return FBInstant.context.getType() === "SOLO" || this._forcedSolo;
    }

    get inGroup(): boolean {
        return this._contextPlayers.length > 2 && !this._forcedSolo;
    }

    get friends(): User[] {
        return this._friends;
    }
    get socialPlatform(): SocialPlatformProvider {
        return "fb";
    }
}
