import {FBAnalyticsProvider, IATracker, TypesAnalytics} from "@dp-instant/analytics";
import {AudioModel, TypesAudio} from "@dp-instant/audio";
import {Action, di, lazyGet, Localize, log, TypesCore, WaitAction} from "@dp-instant/core";
import {
    AdManagerBase,
    IAdManager,
    ReferralRewardsModel,
    SocialPlatformProvider,
    TypesAds,
    TypesApp,
    TypesSocial
} from "@dp-instant/social";
import { TypesUI } from "@dp-instant/ui";
import * as Sentry from "@sentry/browser";
import "firebase/remote-config";
import {inject, injectable} from "inversify";
import {SocialFlowAction} from "./SocialFlowAction";

interface PageRootObject {
    ready: boolean;
    loaded: number;
    params: any;
}
declare const __SOCIAL__: SocialPlatformProvider;

@injectable()
export abstract class StartupActionBase extends SocialFlowAction {
    protected TRACK_EVENT = "game_started";

    @inject(TypesAds.manager)
    protected ads: IAdManager;

    protected get pageRoot(): PageRootObject | undefined {
        return (window as any).__diffbus as PageRootObject;
    }

    async launch(): Promise<any> {
        if (process.env.NODE_ENV === "development") {
            (window as any).social = this.social;
            (window as any).locale = Localize;
            // console.clear();
            console.time("Game started!");
            console.group("Startup");
            log.setLevel(Number.MAX_SAFE_INTEGER); // all logs
        } else {
            log.setLevel(3); // error and above logs
        }
        // @ts-ignore – webpack.DefinePlugin satisfies with __APP__META__
        const APP_META = __APP__META__;
        if (APP_META) {
            APP_META.id ||= APP_META.facebook.id;
            di.rebind(TypesCore.gameConfig).toConstantValue(APP_META);
            if (__SOCIAL__ === "fb") {
                let useFbAnalytics = APP_META.analytics?.useFb;
                if (typeof useFbAnalytics === "undefined") {
                    useFbAnalytics = true;
                }
                if (useFbAnalytics) {
                    lazyGet<IATracker>(TypesAnalytics.tracker)?.addProvider(
                        new FBAnalyticsProvider()
                    );
                }
            }
        }

        lazyGet<Action>("sentryAction")?.run();
        const {i18n} = di.get(TypesCore.gameConfig);
        if (i18n) {
            Localize.addTemplates(i18n);
        }
        await Promise.all([this.initSocialSDK().then(() => this.login()), this.preloadAssets()]);
        this.assetsProgress = 1;
        log.trace("all done");

        // assets preloaded
        await this.social.startGameAsync();
        await this.buildUI();

        Promise.all([
            this.processAssets(),
            // preload ADs
            this.preloadAds()
        ]).then();
        // fetch friends and social stuff
        await this.social.init(this.pageRoot?.params);
        // tell sentry about the context
        (() => {
            const {id, name, photo} = this.social.me;
            Sentry.setUser({id, name, photo});
        })();
        lazyGet<ReferralRewardsModel>(TypesSocial.refRewardsModel)?.init();

        if (process.env.NODE_ENV === "development") {
            console.group("Localizations");
            Localize.templates.forEach((v, k) => {
                console.log(`[${k}]`, Localize.get(k));
            });
            console.groupEnd();
        }

        await this.main();
        await lazyGet<AudioModel>(TypesAudio.model)?.activateBackgroundMusic();

        if (process.env.NODE_ENV === "development") {
            console.groupEnd();
            console.timeEnd("Game started!");
        }
    }

    protected async buildUI(): Promise<void> {
        return lazyGet<Action>(TypesUI.setupAction)?.run();
    }
    protected abstract main(): Promise<void>;

    protected async login(): Promise<void> {
        /*await*/ lazyGet<Action>(TypesApp.loginAction)?.run();
    }
    protected async initSocialSDK(): Promise<void> {
        while (!this.pageRoot?.ready) {
            await WaitAction.ms(10);
        }
        switch (this.social.socialPlatform) {
            case "ya":
                const {yasdk} = this.pageRoot.params;
                // @ts-ignore
                this.ads.init(yasdk);
                break;
        }
    }

    protected needToShowAD(): boolean {
        return false; // FBInstant is not defined error
    }

    protected async preloadAds(): Promise<void> {
        await Promise.all([
            this.ads.preloadNextInter(AdManagerBase.config.ids.INTER),
            this.ads.preloadNextReward(AdManagerBase.config.ids.REWARD)
        ]);
    }
    protected async preloadAssets(): Promise<void> {
        // no assets to preload
    }

    protected async processAssets(): Promise<void> {
        // no assets to process
    }

    private _assetsProgress = 0;
    private _gameStartedAtProgress = NaN;
    get assetsProgress(): number {
        return this._assetsProgress;
    }

    set assetsProgress(value: number) {
        if (this.pageRoot) {
            if (isNaN(this._gameStartedAtProgress)) {
                this._gameStartedAtProgress = this.pageRoot.loaded || 0;
                // @ts-ignore
                if (typeof gsap !== "undefined") {
                    // @ts-ignore
                    gsap.killTweensOf(this.pageRoot);
                }
            }
            this._assetsProgress = value;
            this.pageRoot.loaded =
                this._gameStartedAtProgress +
                (100 - this._gameStartedAtProgress) * this._assetsProgress;
        }
    }
}
