import {ContainerModule, interfaces} from "inversify";
import {AppModel} from "./model/AppModel";
import {TypesApp} from "./TypesApp";
import {LoginAction} from "./actions/LoginAction";

export const AppModule = new ContainerModule((
    bind: interfaces.Bind /*, unbind, isBound, rebind*/
) => {
    bind(TypesApp.model).to(AppModel).inSingletonScope();
    bind(TypesApp.loginAction).to(LoginAction);
});
