import * as Sentry from "@sentry/browser";
import {IPaymentsModel, IPaymentsModelConfig} from "../interfaces";
import {injectable} from "inversify";
import {lazyGet, log, WaitAction} from "@dp-instant/core";
import {IATracker, TypesAnalytics} from "@dp-instant/analytics";
const hasAPI = (str: string) => FBInstant.getSupportedAPIs().includes(str);
const urlRequest = async (url: string, payload: any) => {
    try {
        const rawResponse = await fetch(url, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
            },
            body: JSON.stringify(payload)
        });
        return await rawResponse.json();
    } catch (e) {
        log.warn(e);
    }
    return {};
};
