import {Action, log} from "@dp-instant/core";
import {inject, injectable} from "inversify";
import {ISocial, TypesSocial} from "@dp-instant/social";
import {NotificationLocalizations, NotificationLocalizationsKey} from "../interfaces";
import {TypesNotification} from "./";

@injectable()
export class NotificationAction<T = never> extends Action<T> {
    protected static IMAGES_CACHE: {[key: string]: string} = {};

    static EMOJI = "";
    static readonly WIDTH_FULL = 1200; // TODO: optimize image size
    static readonly HEIGHT_FULL = 627;

    @inject(TypesSocial.model)
    protected social: ISocial;

    @inject(TypesNotification.i18n)
    protected i18n: NotificationLocalizations;

    protected action: FBInstant.UpdateAction = "CUSTOM";
    protected template = "play_turn";
    protected notification = "PUSH";
    protected strategy?: string;
    protected cta?: string;

    async execute(data: T): Promise<any> {
        this.data = data;
        const payload = await this.getPayload();
        const result = await this.social.notify(payload);
        if (result) {
            log.trace("notification", payload.text, payload.strategy);
            if (process.env.NODE_ENV === "development") {
                const image: any = new Image();
                image.crossOrigin = "Anonymous";
                image.src = payload.image;
                image.style.width = "100%";
                const el = document.createElement("div") as HTMLDivElement;
                el.style.width = "100%";
                el.style.position = "absolute";
                el.style.zIndex = "1000";
                const notificationDiv = document.createElement("div") as HTMLDivElement;
                // eslint-disable-next-line prefer-const
                let interval: any;
                const removeEl = () => {
                    clearInterval(interval);
                    document.body.removeChild(el);
                };
                el.onclick = removeEl;
                const text = document.createElement("div") as HTMLDivElement;
                notificationDiv.style.margin = "5px 5px 5px auto";
                notificationDiv.style.opacity = "0.5";
                notificationDiv.style.padding = "10px";
                notificationDiv.style.borderRadius = "10px";
                notificationDiv.style.border = "1px solid #999";
                notificationDiv.style.background = "#f1f1f1";
                notificationDiv.style.right = "0";
                notificationDiv.style.width = "40%";
                notificationDiv.style.maxWidth = "300px";
                text.style.margin = "auto";

                text.innerText = ((text, locale) => {
                    if (typeof text === "object") {
                        return (
                            (text as FBInstant.LocalizableContent).localizations[locale!] ||
                            (text as FBInstant.LocalizableContent).default
                        );
                    }
                    return "" + text;
                })(payload.text, FBInstant.getLocale());

                notificationDiv.appendChild(image);
                notificationDiv.appendChild(text);
                el.appendChild(notificationDiv);
                document.body.insertBefore(el, document.body.firstChild);

                interval = setTimeout(removeEl, 1000);
            }
        } else {
            log.warn("notification", payload.text, payload.strategy);
        }
        this.resolve();
    }

    protected async getPayload(): Promise<FBInstant.CustomUpdatePayload> {
        return {
            action: this.action,
            template: this.template,
            strategy: this.strategy,
            notification: this.notification,
            cta: this.cta,
            text: this.getText(),
            image: await this.getImage()
        };
    }

    protected getEmoji(): string {
        return NotificationAction.EMOJI ? NotificationAction.EMOJI + " " : "";
    }

    protected getI18NKey(): NotificationLocalizationsKey {
        return "play_turn";
    }
    protected getText(): FBInstant.LocalizableContent {
        const emoji = this.getEmoji();
        const replaceUsernameAndScore = (str: string) =>
            emoji +
            str.replace(/{username}|{score}/g, key => {
                switch (key) {
                    case "{username}":
                        return this.social.me.name;
                    case "{score}":
                        return "" + this.social.me.scoreSession;
                }
                return key;
            });
        const transform = (str: FBInstant.LocalizableContent, fn: (str: string) => string) => {
            const obj: FBInstant.LocalizableContent = {
                default: fn(str.default),
                localizations: {}
            };
            for (const key in str.localizations) {
                obj.localizations[key] = fn(str.localizations[key]);
            }
            return obj;
        };
        return transform(this.i18n[this.getI18NKey()], replaceUsernameAndScore);
    }

    protected async getImage(): Promise<string> {
        return this.loadImage("./assets/Notification.png");
    }

    protected async loadImage(path: string, cache = true): Promise<string> {
        if (cache && NotificationAction.IMAGES_CACHE[path]) {
            return NotificationAction.IMAGES_CACHE[path];
        }
        return new Promise<string>(resolve => {
            const image = new Image();
            image.crossOrigin = "Anonymous";
            image.src = path;
            image.style.visibility = "hidden";
            document.body.appendChild(image);
            image.addEventListener("load", () => {
                document.body.removeChild(image);
                const canvas = document.createElement("canvas");
                const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
                canvas.width = image.width;
                canvas.height = image.height;
                ctx.drawImage(image, 0, 0);
                const imageBase64String = canvas.toDataURL("image/jpeg");
                if (cache) {
                    NotificationAction.IMAGES_CACHE[path] = imageBase64String;
                }
                resolve(imageBase64String);
            });
        });
    }
}
