import {log} from "@dp-instant/core";
import {injectable} from "inversify";

@injectable()
export class BackendModel {
    protected _defaultHost: string;

    async performHTTPRequest<T = any>(
        method: string,
        path?: string,
        payload?: any
    ): Promise<T | undefined> {
        try {
            const requestInit: RequestInit = {method};
            if (payload) {
                requestInit.headers = {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                };
                requestInit.body = JSON.stringify(payload);
            }
            if (!this._defaultHost) {
                throw `default host is ${this._defaultHost}`;
            }
            let host = this._defaultHost;
            if (path) {
                host += "/" + path;
            }
            const rawResponse = await fetch(host, requestInit);
            return await rawResponse.json();
        } catch (e) {
            log.warn(e);
        }
        return undefined;
    }

    async post(data: any, path?: string): Promise<any> {
        return this.performHTTPRequest("POST", path, data);
    }
    async get(path?: string): Promise<any> {
        return this.performHTTPRequest("GET", path);
    }
    public get defaultHost(): string {
        return this._defaultHost;
    }
}
