import {inject, injectable} from "inversify";
import {Action, IGameConfigJSON, lazyGet, log, TypesCore} from "@dp-instant/core";
import {TypesApp, AppModel} from "@dp-instant/social";
import * as Sentry from "@sentry/browser";
import {Integrations} from "@sentry/tracing";
const getJSONFromRemoteConfig = (
    remoteConfig: any | undefined,
    key: string,
    defaultValue?: any
) => {
    const value = remoteConfig?.getValue(key);
    if (value) {
        try {
            const json = JSON.parse(value.asString());
            if (json) {
                return json;
            }
        } catch (e) {
            log.warn(`Failed to get ${key} from remote`);
        }
    }
    return defaultValue;
};

@injectable()
export class SetupSentryAction extends Action<any> {
    @inject(TypesCore.gameConfig)
    protected gameConfig: IGameConfigJSON;

    async execute(params: any): Promise<any> {
        const {id: gameId} = this.gameConfig;
        const config = this.getConfig();

        Sentry.init({
            ...config,
            integrations: [new Integrations.BrowserTracing()],
            tracesSampleRate: 1.0 // We recommend adjusting this in production
        });
        Sentry.configureScope(scope => {
            const normalizeUrl = (url: string) =>
                url.replace(
                    /^(.*:)\/\/([A-z0-9\-.]+)(:[0-9]+)?\/(.*)\//,
                    `~/${gameId}/${config!.release}/`
                );

            scope.addEventProcessor(async (event: any) => {
                if (event.culprit) {
                    event.culprit = normalizeUrl(event.culprit);
                }

                if (event.exception?.values[0]?.stacktrace?.frames) {
                    event.exception.values[0].stacktrace.frames = event.exception.values[0].stacktrace.frames.map(
                        (frame: any) => {
                            frame.filename = normalizeUrl(frame.filename);
                            return frame;
                        }
                    );
                }

                return event;
            });
        });
    }
    private getConfig() {
        const appModel: AppModel | undefined = lazyGet(TypesApp.model);
        const {sentry: origConfig} = this.gameConfig;
        if (appModel) {
            const sentryConfig = getJSONFromRemoteConfig(
                appModel.remoteConfig,
                "sentry",
                origConfig
            );
            if (sentryConfig) {
                sentryConfig.release = origConfig?.release;
                return sentryConfig;
            }
        }
        return origConfig;
    }
}
