import {ContainerModule, interfaces} from "inversify";
import {di, IGameConfigJSON, Random, TypesCore} from "@dp-instant/core";
import {
    CrossPromoConf,
    CrossPromoIconConf,
    CrossPromoComponent,
    TypesPromo
} from "@dp-instant/crosspromo";
// TODO: fix tsc compilation (incremental) with json
// import * as config_json from "../../crosspromo.json";
const config_json = {
    games: [
        {
            appId: "778673846242493",
            icon: "logo-chain-cube.jpg",
            title: "Chain Cube",
            subtitle: "2048"
        },
        {
            appId: "258766835191242",
            icon: "logo-blocks.jpg",
            title: "Nine Blocks",
            subtitle: "Game"
        },
        // {
        //     appId: "1470645013129858",
        //     icon: "logo-fashion.jpg",
        //     title: "Spot the difference",
        //     subtitle: "Fashion"
        // },
        {
            appId: "611639816037657",
            icon: "logo-hidden-object.jpg",
            title: "Hidden Objects",
            subtitle: "Brain Teaser"
        }
    ]
};

const config = config_json as unknown as CrossPromoConf;

export const CrossPromoModule = new ContainerModule(
    (bind: interfaces.Bind, unbind: interfaces.Unbind, isBound: interfaces.IsBound) => {
        bind(TypesPromo.config).toConstantValue(config);
        const allGames: CrossPromoIconConf[] = config.games;

        bind(TypesPromo.random).toDynamicValue(() => {
            const appDefines: IGameConfigJSON = di.get(TypesCore.gameConfig);
            return allGames
                .sort(<() => number>Random.sign)
                .filter(g => g.appId !== appDefines.id)[0];
        });

        bind(TypesPromo.randomComponent).toDynamicValue(() => {
            if (isBound(TypesPromo.component)) {
                const shuffledGames = allGames.sort(<() => number>Random.sign);
                for (const config of shuffledGames) {
                    if (isBound(config.icon)) {
                        const icon = di.get<CrossPromoComponent>(TypesPromo.component);
                        icon.config = config;
                        return icon;
                    }
                }
            }
        });
    }
);
