import {Action} from "@dp-instant/core";
import {inject, injectable} from "inversify";
import {TypesAds, AdResponse, IAdManager, AdManagerBase} from "./";

export interface AdActionPayload {
    reward?: boolean;
    placement?: string;
    preloadNext?: boolean;
}

@injectable()
export class AdAction<T extends AdActionPayload | boolean | never = never> extends Action<T> {
    @inject(TypesAds.manager)
    private ads: IAdManager;

    async execute(data?: T): Promise<any> {
        let placement: string | undefined = undefined;
        let reward = false;
        let preloadNext = true;
        if (typeof data === "boolean") {
            reward = data;
        } else if (typeof data !== "undefined") {
            placement = (data as AdActionPayload).placement;
            reward = <boolean>(data as AdActionPayload).reward;
            preloadNext = <boolean>(data as AdActionPayload).preloadNext;
        }
        await this.showAd(placement, reward, preloadNext);
        this.resolve();
    }

    public async showAd(
        placement?: string,
        isReward = false,
        preloadNext = true
    ): Promise<AdResponse> {
        let status = AdResponse.UNKNOWN;
        if (isReward) {
            status = await this.ads.showRewardAd(
                placement || AdManagerBase.config.ids.REWARD,
                preloadNext
            );
        } else {
            status = await this.ads.showAd(
                placement || AdManagerBase.config.ids.INTER,
                false,
                preloadNext
            );
        }
        return status;
    }
    /**
     * @deprecated use showAd
     */
    public async showRewardAd(
        placement: string = AdManagerBase.config.ids.REWARD,
        preloadNext = true
    ): Promise<AdResponse> {
        return this.showAd(placement, true, preloadNext);
    }
}
